.lk-my-orders {
	.product-content__cards-list {
		gap: 20px;
	}
}

.tabs-margin {
	margin-top: 40px;
}

.lk-my-orders__btn {
	@media (min-width: 992px) {
		margin-top: 73px;
	}
}

.lk-my-orders__order-card {
	@media (min-width: 992px) {
		max-width: 898px;
	}
}

.order-card {
	border: 1px solid #eee;
	padding: 20px;
	width: 100%;
}

.order-card__title {
	display: block;
	font-weight: 600;
	font-size: 20px;
	color: var(--bs-font-color);
	text-decoration: none;
	line-height: 1.2;
	transition: all 0.2s ease;

	@media (min-width: 768px) {
		font-size: 24px;
	}
}

.order-card__head {
	display: flex;
	gap: 4px;
	flex-direction: column;

	@media (min-width: 992px) {
		flex-direction: row;
		gap: 35px;
		justify-content: space-between;
	}
}

.order-card__price-overview {
	font-weight: 600;
	font-size: 20px;
	color: var(--primary-color);
	flex-shrink: 0;
}

.order-card__category {
	margin-top: 10px;
	line-height: 1;
	font-weight: 400;
	font-size: 14px;
	color: var(--gray-color);
}

.order-card__order-card-statistic {
	margin-top: 18px;

	@media (min-width: 768px) {
		margin: 0;
	}
}

.order-card-statistic {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	align-items: center;
	gap: 10px;

	@media (min-width: 768px) {
		gap: 25px;
	}

	@media (min-width: 992px) {
		gap: 35px;
		min-width: 340px;
	}
}

.order-card-statistic__item {
	display: flex;
	flex-wrap: nowrap;
	font-size: 14px;
	align-items: center;
	gap: 8px;

	p {
		margin: 0;
	}
}

.order-card__bottom {
	display: flex;
	gap: 16px;
	flex-direction: column;

	@media (min-width: 768px) {
		flex-direction: row;
		align-items: center;
		margin-top: 20px;
		justify-content: space-between;
	}
}

.order-card-statistic__status {
	background: var(--bs-bg-gray);
	border-radius: 48px;
	padding: 6px 16px;
	color: var(--bs-font-color);
	line-height: 1.2;

	&--in-work {
		background: var(--status-in-work);
		color: #fff;
	}

	&--closed {
		background: var(--status-closed);
		color: #fff;
	}
}

.order-card__button {
	.btn {
		width: 100%;
	}
}