.lk-find-order {
	.lk-my-orders__order-card {
		@media (min-width: 768px) {
			width: calc(100% / 2 - 10px);
		}

		@media (min-width: 1200px) {
			max-width: 100%;
			width: 100%;
		}

		.order-card__button {
			@media (min-width: 1200px) {
				margin-top: auto;
			}

			.btn {
				@media (min-width: 1200px) {
					min-width: 220px;
				}
			}
		}
	}

	.order-card__category {
		margin: 0;
		font-weight: 300;
		color: var(--bs-body-color);

		@media (min-width: 1200px) {
			font-size: 16px;
		}
	}

	hr {
		margin-top: 0;
		margin-bottom: 20px;
	}
}

.lk-find-order--detail {
	.order-card {
		margin-top: 15px;
		box-shadow: none;
		border-color: transparent;
		width: 100%;
		max-width: 100%;

		@media (min-width: 992px) {
			padding: 40px;
		}

		.order-card__head {
			flex-direction: column;
			gap: 12px;

			@media (min-width: 768px) {
				gap: 20px;
			}
		}

		.order-card__title,
		.order-card__price-overview {
			font-size: 24px;
			margin: 0;

			@media (min-width: 768px) {
				font-size: 30px;
			}

			@media (min-width: 992px) {
				font-size: 38px;
			}
		}

		.order-card-statistic {
			flex-direction: column;
			align-items: flex-start;
			margin-top: 15px;

			@media (min-width: 768px) {
				flex-direction: row;
				gap: 30px;
				margin-top: 30px;
				margin-bottom: 40px;
			}
		}

		.order-card__category,
		.order-card-statistic__item {
			font-size: 16px;
			color: var(--gray-color);

			@media (min-width: 768px) {
				font-size: 18px;
			}

			@media (min-width: 992px) {
				font-size: 20px;
			}
		}

		.order-card__text {
			display: block;
			-webkit-line-clamp: none;
			overflow:auto;
			font-size: 16px;
			line-height: 1.4;
			color: var(--bs-font-color);
			font-weight: 400;

			@media (min-width: 768px) {
				font-size: 18px;
			}

			@media (min-width: 992px) {
				font-size: 20px;
			}
		}

		.order-card__details {
			margin: 0 0 20px;

			@media (min-width: 768px) {
				width: 350px;
				margin: 10px 0 20px;
				flex-direction: row;
				gap: 20px;
			}

			@media (min-width: 992px) {
				width: 375px;
				margin: 20px 0 40px;
				gap: 40px;
			}
		}

		.order-card__detail {
			@media (min-width: 768px) {
				font-size: 20px;
			}
		}

		.order-card__conditions {
			@media (min-width: 1200px) {
				display: block;
			}
		}

		.order-card__button {
			@media (min-width: 576px) {
				width: 220px;
			}
		}
	}
}

.order-card__order-statistics {
	display: flex;
	gap: 16px;
	flex-direction: column;

	@media (min-width: 768px) {
		flex-direction: row;
		align-items: center;
		justify-content: space-between;
	}

	.order-card-statistic {
		justify-content: flex-start;
		row-gap: 10px;
		column-gap: 15px;
		margin-top: 10px;
		margin-bottom: 20px;

		& > * {
			line-height: 1;
		}
	}
}

.order-card__text {
	display: -webkit-box;
	-webkit-line-clamp: 2;
	-webkit-box-orient: vertical;
	overflow: hidden;
	font-weight: 300;
	font-size: 14px;
	line-height: 140%;
	color: var(--gray-color);

	@media (min-width: 1200px) {
		font-size: 16px;
	}

	& > *:last-child {
		margin-bottom: 0;
	}
}

.order-card__link {
	font-weight: 600;
	font-size: 14px;
	color: var(--primary-color);
	display: block;
	margin-top: 5px;

	@media (min-width: 1200px) {
		font-size: 16px;
	}
}

.order-card__conditions {
	@media (min-width: 1200px) {
		display: flex;
		flex-wrap: nowrap;
		justify-content: space-between;
	}
}

.order-card__details {
	margin-top: 10px;
	display: flex;
	flex-wrap: wrap;
	row-gap: 12px;
	column-gap: 15px;

	@media (min-width: 360px) {
		width: 295px;
	}

	@media (min-width: 768px) {
		flex-direction: column;
	}

	@media (min-width: 992px) {
		flex-direction: row;
		width: 300px;
		justify-content: space-between;
	}

	@media (min-width: 1200px) {
		margin-top: 6px;
	}

	& > *:first-child {
		flex-grow: 1;
	}
}

.order-card__detail {
	display: flex;
	align-items: center;
	gap: 5px;
	color: var(--gray-color);
	font-weight: 300;

	i {
		font-size: 20px;
	}

	@media (min-width: 768px) {
		font-size: 16px;
	}
}

.profile-avatar-and-name {
	display: flex;
	align-items: center;
	flex-wrap: nowrap;
	gap: 10px;
	text-decoration: none;
	width: 100%;

	.profile-avatar-and-name__pic {
		width: 50px;
		height: 50px;
	}

	img {
		max-width: 100%;
		border-radius: 50%;
	}

	.profile-avatar-and-name__name {
		font-weight: 400;
		font-size: 16px;
		color: var(--bs-font-color);
	}
}

.order-card__info {
	margin-top: 20px;

	@media (min-width: 768px) {
		margin-top: 40px;
	}
}

.order-card__section-title {
	font-weight: 600;
	font-size: 20px;
	line-height: 140%;
	color: var(--bs-font-color);
	margin-bottom: 20px;

	@media (min-width: 768px) {
		font-size: 24px;
	}

	@media (min-width: 992px) {
		font-size: 32px;
	}
}

.files-list {
	list-style: none;
	padding: 0;

	li {
		display: flex;
		gap: 10px;
		margin-bottom: 15px;

		&:last-child {
			margin-bottom: 0;
		}
	}

	i {
		position: relative;
		//top: 2px;
	}

	a {
		font-weight: 400;
		font-size: 16px;
		color: var(--primary-color);
		text-decoration: none;
		line-height: 1.2;

		@media (min-width: 768px) {
			font-size: 20px;
		}
	}
}