.breadcrumbs {
	padding: 10px 0 10px;

	@media (min-width: 992px) {
		padding: 30px 0 20px;
	}

	&.light {
		.breadcrumb-divider,
		.breadcrumb-item a {
			color: #b5b5b5;
		}

		.breadcrumb-item a {
			&:hover {
				color: #6f6f6f;
			}
		}
	}
}

.breadcrumb {
	color: #6f6f6f;
	font-weight: 400;
	font-size: 12px;
	margin-bottom: 0;

	@media (min-width: 992px) {
		font-size: 16px;
	}

	li {
		a {
			text-decoration: none;
			color: #6f6f6f;

			&:hover {
				color: var(--primary-color);
			}
		}
	}

	.breadcrumb-divider {
		display: flex;
		align-items: center;
		margin-left: 2.5px;
		margin-right: 5px;

		@media (min-width: 992px) {
			margin-left: 5px;
			margin-right: 10px;
		}

		i {
			display: block;
			transform: scaleX(-1);
			font-size: 12px;
		}
	}
}

.breadcrumb-item+.breadcrumb-item::before {
	content: none;
}

