.modal {
	.btn-close {
		position: absolute;
		top: -45px;
		right: 0;
		font-size: 20px;
		filter: brightness(100);
		background-image: none;
		outline: none;
		box-shadow: none;
		z-index: 1;
		pointer-events: auto;
		cursor: pointer;

		@media (min-width: 768px) {
			right: -5px;
		}

		@media (min-width: 1200px) {
			top: -5px;
			right: -50px;
		}

		.icon-cross {
			color: #fff;
		}
	}

	&.full-height {
		.btn-close {
			top: -32px;
			right: 14px;

			@media (min-width: 576px) {
				top: -40px;
				right: 12px;
			}

			@media (min-width: 768px) {
				top: -36px;
				right: -5px;
			}

			@media (min-width: 1200px) {
				top: -5px;
				right: -50px;
			}

			.icon-cross {
				color: #fff;
			}
		}

		.modal-dialog {
			margin-top: 20px;

			height: calc(100dvh - 100px);

			@media (max-width: 575px) {
				margin: 0;
			}

			@media (min-width: 768px) {
				margin-top: 40px;
			}

			@media (min-width: 1200px) {
				margin-top: 1.75rem;
			}
		}

		.modal-content {
			margin-top: 20px;
			height: calc(100dvh - 160px);

			@media (min-width: 768px) {
				margin-top: 0;
				height: calc(100dvh - 75px);
			}
		}

		.modal-body__wrap {
			height: calc(100dvh - 75px);
			overflow-y: auto;
		}

		.modal-body {
			padding: 0;
			overflow: hidden;
		}

		.modal-body__wrap {
			padding: 16px;

			@media (min-width: 768px) {
				padding: 40px;
			}
		}
	}

	&.normal-height {
		.btn-close {
			top: -32px;
			right: 14px;

			@media (min-width: 576px) {
				top: -40px;
				right: 12px;
			}

			@media (min-width: 768px) {
				top: -36px;
				right: -5px;
			}

			@media (min-width: 1200px) {
				top: -5px;
				right: -50px;
			}

			.icon-cross {
				color: #fff;
			}
		}

		.modal-dialog {
			margin-top: 20px;
			height: calc(100dvh - 100px);

			@media (max-width: 575px) {
				margin: 0;
			}

			@media (min-width: 768px) {
				margin-top: 40px;
			}

			@media (min-width: 1200px) {
				margin-top: 1.75rem;
			}
		}

		.modal-content {
			margin-top: 20px;
			height: calc(100dvh - 78px);

			@media (min-width: 768px) {
				margin-top: 0;

			}
		}

		.modal-body__wrap {
			height: calc(100dvh - 75px);
			overflow-y: auto;
		}

		.modal-body {
			padding: 0;
			overflow: hidden;
		}

		.modal-body__wrap {
			padding: 16px;

			@media (min-width: 768px) {
				padding: 40px;
			}
		}
	}

	.modal-header {
		border-bottom: none !important;

		&.border-bottom {
			border-bottom: 1px solid #ececec !important;
		}

		.section-title {
			font-size: 28px;

			@media (min-width: 768px) {
				font-size: 32px;
			}

			@media (min-width: 992px) {
				font-size: 38px;
			}

			margin: 0;
		}

		.modal-title--small-mobile {
			@media (max-width: 479px) {
				font-size: 22px;
			}
		}

		&--padding {
			@media (min-width: 768px) {
				padding: 20px 40px;
			}
		}
	}

	.modal-content {
		border-radius: 20px;
	}

	.modal-body {
		background-color: #fff;
		border-radius: 0 0 20px 20px;

		&.border-radius {
			border-radius: 20px;
		}
	}

	.modal-body__wrapper {
		overflow: hidden;
		border-radius: 20px;
		overflow-y: auto;
	}

	.form-check-label {
		font-weight: 400;
		font-size: 16px;
		line-height: 140%;
		color: #222;
	}

	.editor-form__group {
		margin-bottom: 28px;
	}

	.editor-form__title {
		font-size: 16px;
		margin-bottom: 10px;
	}

	.modal__form-element-link {
		font-weight: 600;
		font-size: 14px;
		line-height: 1.4;
		color: var(--primary-color);
		text-decoration: none;
	}
}

.modal.vacancyModal {
	.modal-dialog {
		@media (min-width: 992px) {
			max-width: 898px;
		}
	}

	.modal-header {
		padding: 0;
		border: none;
	}

	.modal-content {
		position: relative;
		//overflow: visible;
		padding: 0;
		border: none;
		//overflow: hidden;
	}

	.vacancies__item {
		box-shadow: none;
	}

	.modal__response {
		margin-bottom: 88px;

		@media (min-width: 768px) {
			margin-bottom: 0;
		}
	}
}

.modal.addWorkModal {
	.modal-header {
		border: none;
	}

	.modal-body__wrap {
		padding: 0 16px 16px;

		@media (min-width: 992px) {
			padding: 0 40px 40px;
		}
	}

	.modal-body {
		border-radius: 0 0 20px 20px;
	}

	.modal-dialog {
		//width: 100%;
		@media (min-width: 992px) {
			max-width: 856px;
		}
	}

	.editor-form__file-picker {
		&.only-one-photo {
			height: 268px;


			& + .editor-form__photo-item {
				height: 268px;
				width: 100% !important;
			}

			.editor-form__file-picker,
			.editor-form__photo-item {

			}
		}
	}

	.editor-form__file-picker,
	.editor-form__photo-item {
		width: calc(100% / 3 - 6px);

		@media (min-width: 992px) {
			width: calc(100% / 3 - 11px);
		}
	}
}

.modal.serviceOffer {
	.order-card__details {
		width: 100%;
		margin-bottom: 0 !important;

		@media (min-width: 768px) {
			flex-direction: row;
			width: 295px;
		}
	}

	.modal-header {
		@media (min-width: 1200px) {
			padding: 20px 30px 20px 30px;
		}
	}

	.modal-body__wrap {
		padding: 20px;

		@media (min-width: 1200px) {
			padding: 20px 30px 30px 30px;
		}
	}

	.modal-dialog {
		width: 100%;

		@media (min-width: 768px) {
			max-width: 650px;
		}

		@media (min-width: 1200px) {
			max-width: 978px;
		}
	}

	.order-card {
		box-shadow: none;
		padding: 16px;
		border-color: #d0d0d0;
		max-width: 100%;
	}

	.order-card__category {
		margin-top: 0;
	}

	.editor-form__group {
		margin-top: 28px;
	}

	.editor-form__title {
		font-weight: 600;
		font-size: 24px;
	}

	.order-card__text {
		-webkit-line-clamp: 4;
	}

	.editor-form textarea {
		min-height: 100px;
	}
}

.modal.addReview {
	.modal-dialog {
		@media (min-width: 1200px) {
			max-width: 800px;
		}
	}

	.product-card__profile {
		display: flex;
		justify-content: center;
		flex-direction: column;
		text-align: center;
		align-items: center;
	}

	.product-card__user-pic {
		width: 100px;
		height: 100px;

		@media (min-width: 768px) {
			width: 150px;
			height: 150px;
		}

		@media (min-width: 1200px) {
			width: 200px;
			height: 200px;
		}

		img {
			border-radius: 4px;
		}
	}

	.product-card__profile-info {
		align-items: center;
		margin-top: 20px;
	}

	.comment-item__profile-rating {

	}

	.comment-item__profile-rating-icon {
		img {
			height: 32px;

			@media (min-width: 768px) {
				height: 40px;
			}

			@media (min-width: 1200px) {
				height: 50px;
			}
		}
	}

	.editor-form__title {
		@media (min-width: 992px) {
			font-size: 24px;
			margin-bottom: 20px;
		}
	}

	textarea {
		@media (min-width: 768px) {
			min-height: 19vh;
		}

		@media (min-width: 992px) {
			min-height: 16vh;
		}

		@media (min-width: 1200px) {
			min-height: 24vh;
		}
	}

	.form__main-buttons {
		margin-top: auto;
	}
}

.modal.addOrder {
