.modal.editWorkModal {
	.modal-dialog {
		@media (min-width: 768px) {
			max-width: 570px;
		}

		@media (min-width: 992px) {
			max-width: 875px;
		}
	}

	.editor-form__file-picker.only-one-photo,
	.editor-form__file-picker.only-one-photo + .editor-form__photo-item {
		height: 268px;
	}

