.modal.loginModal {
	.modal-dialog {
		max-width: 375px;
	}

	.modal-header {
		flex-direction: row;
		flex-wrap: wrap;
	}

	.modal-header__subtitle {
		display: flex;
		font-weight: 400;
		font-size: 16px;
		line-height: 1.4;
		color: #6f6f6f;
		flex-direction: column;

		a {
			text-decoration: none;
			color: var(--primary-color);

			&:hover {
				color: var(--primary-color-hover);
			}
		}
	}

	.modal-content {
		background-color: transparent;
		border: none;
	}

	[data-window-switch=container] {
		border-radius: 20px;
	}

	.modal-content__inner {
		background-color: #fff;
		border-radius: 20px;
		border: 1px solid #d0d0d0;
	}

	.modal__form-element {
		&:not(:last-child) {
			margin-bottom: 10px;
		}
	}

	input.form-control {
		border-radius: 10px;
	}

	.modal__new-phone-request {
		font-weight: 400;
		font-size: 16px;
		line-height: 1.4;
		color: #6f6f6f;
		margin-top: 14px;

		a {
			text-decoration: none;
			color: var(--primary-color);

			&:hover {
				color: var(--primary-color-hover);
			}
		}
	}

	.modal-header__change-number {
		&.hidden {
			display: none;
		}

		&.active {
			display: block;
		}
	}

	.modal-footer {
		display: flex;
		gap: 15px;
		justify-content: flex-start;

		& > * {

		}

		span {
			font-weight: 400;
			font-size: 16px;
			line-height: 1.4;
			color: #6f6f6f;
		}

		.btn {
			max-height: 49px;
			display: inline-flex;
			flex-direction: column;
			justify-content: center;
			flex-grow: 1;
		}
	}
}



.modal-margin-fix {
	margin-bottom: 170px;

	@media (min-width: 768px) {
		margin-bottom: 88px;
	}
}

.modal__text {
	margin-top: 40px;
}

.modal__text-item {
	margin-bottom: 20px;
}

.modal__text-title {
	margin-bottom: 30px;
	font-weight: 600;
	font-size: 24px;

	@media (min-width: 992px) {
		font-size: 32px;
	}
}

.modal__response {
	border-radius: 10px;
	padding: 30px 16px;
	background: linear-gradient(270deg, #1db6e7 0%, #ccfcfc 100%);

	@media (min-width: 992px) {
		padding: 30px 38px;
		margin-top: 40px;
	}

	.form-control {
		border: 1px solid #d0d0d0;
		border-radius: 10px;

		@media (min-width: 768px) {
			font-size: 14px;
		}

		@media (min-width: 992px) {
