		}
	}
}

.modal__response-title {
	font-weight: 600;
	font-size: 24px;
	color: var(--bs-title-color);
	margin-bottom: 30px;

	@media (min-width: 992px) {
		font-size: 38px;
	}
}



.drop-zone {
	border: 1px dashed #222;
	border-radius: 10px;
	padding: 22px 20px;
	text-align: center;
	cursor: pointer;
	transition: 0.3s;
	position: relative;
	margin: auto;
	background: #fff;
	//display: flex;

	&.files-added {
		.drop-zone__element {
			justify-content: flex-start;
		}

		.fileName {
			//margin-top: 10px;

			ul {
				li {
					color: var(--gray-color);
				}
			}
		}
	}

	&.smaller-zone {
		border: 1px dashed #c0c0c0;
		padding: 10px;
		background: transparent;

		&:hover {
			background: transparent;
		}

		.drop-zone__element {
			gap: 6px;

			svg {
				width: 20px;
				height: 20px;
			}
		}
	}

	&[data-multiple-files] {
		ul {
			margin-top: 10px;
			column-count: 1;
			margin-bottom: 0;

			li {
				&:not(:last-child) {
					margin-bottom: 10px;
				}
			}
		}
	}


	&:hover {
		background: #f8f9fa;
	}

	&.dragover {
		background: #e9ecef;
		border-color: #007bff;
	}

	.file-list {
		margin-top: 20px;
		list-style: none;
		padding: 0;
	}

	.file-item {
		display: flex;
		align-items: center;
		justify-content: space-between;
		background: #eeeeee;
		padding: 5px 10px;
		border-radius: 5px;
		margin-bottom: 5px;
		font-size: 14px;

		&:last-child {
			margin: 0;
		}
	}

	.remove-file {
		cursor: pointer;
		color: red;
		font-weight: bold;
		font-size: 18px;
	}

	.remove-file:hover {
		color: darkred;
	}

	.drop-zone__description {
		align-self: center;
	}

	.drop-zone__element {
		display: flex;
		flex-wrap: nowrap;
		gap: 10px;
		justify-content: center;

		svg {
			width: 26px;
			height: 26px;
		}

		& > * {
			font-weight: 400;
			font-size: 16px;
			line-height: 1.2;
		}

		a {
			text-decoration: none;
			color: var(--primary-color);
		}
	}
}


