.main-hero {

	& + .services-carousel {
		margin-top: -130px;
		margin-bottom: 30px;

		@media (min-width: 576px) {
			margin-bottom: 20px;
		}

		@media (min-width: 768px) {
			margin-bottom: 0;
		}

		@media (min-width: 992px) {
			margin-top: -175px;
		}
	}

	& + .announcement-map {
		margin-top: -130px;
		margin-bottom: 30px;

		@media (min-width: 576px) {
			margin-bottom: 20px;
		}

		@media (min-width: 768px) {
			margin-bottom: 0;
		}

		@media (min-width: 992px) {
			margin-top: -175px;
		}
	}

	&--with-image {
		.main-hero__plate {
			border-radius: 0;
			position: relative;
			//padding: 30px 25px 90px;
			background-position: top left -30px;

			@media (min-width: 768px) {
				padding: 100px 0 160px;
				background-position: center;
				background-size: 140%;
			}

			@media (min-width: 992px) {
				padding: 147px 0 200px;
			}


			& > * {
				position: relative;
				z-index: 2;
			}

			&::after {
			  position: absolute;
			  content: '';
			  bottom: 0;
			  right: 0;
			  width: 100%;
			  height: 100%;
				background-image: url('../img/bg/mian-hero-image-full.png');
				background-repeat: no-repeat;
				background-position: bottom -127px left calc(50%);
				background-size: 285px;
				pointer-events: none;

				@media (min-width: 768px) {
					background-size: 405px;
					background-position: bottom -203px left calc(50% + 230px);
				}

				@media (min-width: 992px) {
					background-position: bottom -146px left calc(50% + 285px);
					background-size: 470px;
				}

				@media (min-width: 1200px) {
					background-position: bottom -158px left calc(50% + 479px);
					background-size: 506px;
				}
			}
		}

		& + .services-carousel {
			margin-top: -45px;
			margin-bottom: 10px;

			@media (min-width: 576px) {
				margin-top: -65px;
			}

			@media (min-width: 768px) {
				margin-bottom: 0;
			}

			@media (min-width: 992px) {
				margin-top: -72px;
			}
		}
	}

	&--inner {
		.section-title--main {
			@media (min-width: 360px) {
				max-width: 222px;
				font-size: 22px;
			}

			@media (min-width: 576px) {
				max-width: 60%;
				font-size: 25px;
			}

			@media (min-width: 768px) {
				max-width: 55%;
			}
		}

		.main-hero__plate {
			padding: 25px 15px 170px;

			@media (min-width: 576px) {
				padding: 25px 15px 200px;
			}

			@media (min-width: 768px) {
				padding: 25px 15px 160px;
			}

			@media (min-width: 992px) {
				padding: 90px 40px 230px;
			}

			@media (min-width: 1200px) {
				padding: 90px 40px 260px;
			}
		}

		.main-hero__form {
			max-width: 230px;

			@media (min-width: 576px) {
				max-width: 60%;
			}

			@media (min-width: 768px) {
				max-width: unset;
			}

			@media (min-width: 1200px) {
			}
		}
	}

	&--request {
		.main-hero__plate {
			background-size: cover;

			@media (min-width: 992px) {
				padding-bottom: 60px;
			}

			@media (min-width: 1200px) {
				padding-bottom: 100px;
			}

			&::after {
				background-position: bottom -151px left calc(50% + 114px);
				background-size: 338px;

				@media (min-width: 768px) {
					transform: scale(1, 1);
				}

				@media (min-width: 992px) {
					transform: scale(-1, 1);
					background-position: bottom -77px left calc(50% + 231px);
					background-size: 358px;
				}

				@media (min-width: 1200px) {
					transform: scale(1);
					background-size: 565px;
					background-position: bottom 65px left calc(50% + 418px);
				}

				@media (min-width: 1400px) {
					background-position: bottom 65px left calc(50% + 460px);
				}
			}
		}

		form {
			display: flex;
			flex-direction: column;
			gap: 20px;
			margin-bottom: 110px;

			@media (min-width: 576px) {
				flex-direction: row;
				flex-wrap: wrap;
			}

			@media (min-width: 992px) {
				row-gap: 40px;
				column-gap: 20px;
			}

			@media (min-width: 1200px) {
				margin-bottom: 0;
				max-width: 60%;
			}

			@media (min-width: 1400px) {
				max-width: 60%;
				margin-bottom: 0;
			}
		}

		.request__item {
			position: relative;
			flex-grow: 1;
			width: 100%;
			height: fit-content;

			@media (min-width: 576px) {
				&:nth-child(1) {
					width: calc(50% - 20px);
				}

				&:nth-child(2) {
					width: calc(50% - 20px);
				}

				&:nth-child(3) {
					width: calc(50% - 20px);
				}

				&:nth-child(4) {
					width: calc(50% - 20px);
				}
			}

			@media (min-width: 992px) {
				&:not(:last-child) {
					width: calc(100% / 3 - 20px) !important;
				}

				&:last-child {
					display: flex;
					justify-content: flex-end;
				}
			}

			@media (min-width: 1200px) {
				&:not(:last-child) {
					width: calc(100% / 2 - 20px) !important;
				}

				&:last-child {
					justify-content: flex-start;
				}
			}

			.valid-feedback,
			.invalid-feedback {
				position: absolute;
				right: 15px;
				bottom: -18px;
				text-align: right;
			}
		}
	}
}

.main-hero__form {
	max-width: 320px;
	display: flex;
	gap: 10px;
	flex-wrap: wrap;

	@media (min-width: 375px) {
		max-width: 345px;
		margin-left: auto;
		margin-right: auto;
	}

	@media (min-width: 768px) {
		margin-left: 0;
		max-width: 100%;
	}

	button[type="submit"] {
		margin-left: auto;
		padding-top: 8px;
		padding-bottom: 8px;

		@media (min-width: 768px) {
			margin-left: 0;
		}

		@media (min-width: 1200px) {
			min-height: 58px;
			min-width: 198px;
			font-size: 20px;
		}
	}

	.form-label {
		@media (min-width: 768px) {
			margin-bottom: 0;
		}
	}

	.form-text {
		font-size: 14px;
		line-height: 150%;

		@media (min-width: 768px) and (max-width: 991px) {
			padding-left: 18px;
		}

		@media (min-width: 992px) {
			text-align: center;
		}

		@media (min-width: 1200px) {
			text-align: left;
			font-size: 16px;
			margin-top: 12px;
		}
	}

	.input-group {
		flex-wrap: nowrap;

		@media (min-width: 768px) {
			width: unset;
		}

		@media (min-width: 1200px) {
			min-height: 58px;
		}

		.btn {
			display: flex;
			flex-wrap: nowrap;
			max-height: 43px;
			font-weight: 600;
			font-size: 12px;
			color: #0069b5;
			background-color: #fff;
			align-items: center;
			border-radius: 0 30px 30px 0;
			padding-left: 14px;
			justify-content: center;

			@media (max-width: 374px) {
				width: 100%;
			}

			@media (min-width: 576px) {
				padding: 12.5px 26px 12.5px 10px;
			}

			@media (min-width: 1200px) {
				min-height: 58px;
				font-size: 16px;
			}

			i {
				font-size: 16px;
				margin-right: 5px;

				@media (min-width: 1200px) {
					font-size: 22px;
				}
			}

			&:hover {
				background-color: var(--primary-color);
				color: #fff;
			}
		}
	}

	.input-group-append {
		width: 100%;
	}

	.select2-selection {
		border-radius: 30px 0 0 30px;
	}

	.select2-container--open .select2-selection--single {
		border-top-left-radius: 20px !important;

		@media (min-width: 1200px) {
			border-top-left-radius: 30px !important;
			border-bottom-left-radius: 30px !important;
		}
	}

	.service-selection {
		border-radius: 30px 0 0 30px;
		border: none;
		font-weight: 400;
		font-size: 12px;
		color: #b5b5b5;
		padding: 13.5px 19px 14.5px;
		max-width: 229px;
		outline: none;
	}

	.select2-container {
		width: 190px !important;

		@media (min-width: 360px) {
			width: 220px !important;
		}

		@media (min-width: 375px) {
			width: 245px !important;
		}

		@media (min-width: 1200px) {
			width: 500px !important;
		}
	}

	.select2-selection--single {
		//border-radius: 30px 0 0 30px;
		border: none;
		font-weight: 400;
		font-size: 12px;
		color: #b5b5b5;
		outline: none;
		min-height: 43px;
		display: flex;
		flex-direction: column;
		justify-content: center;

		@media (min-width: 1200px) {
			min-height: 58px;
		}
	}

	.select2-selection__arrow {
		height: calc(50% + 2px) !important;
		top: 50% !important;
		transform: translateY(-50%);
		border-right: 1px solid rgba(150,150,150,0.2);

		b {
			display: none;
		}
	}

	.select2-selection__rendered {
		padding-left: 20px !important;

		@media (min-width: 1200px) {
			font-size: 16px;
			padding-left: 30px !important;
		}
	}

	.select2-search--dropdown .select2-search__field {
		border: none;
		border-bottom: 1px solid rgba(150,150,150,0.2);
		outline: none;
	}

	.select2-dropdown {
		border: none;
		border-radius: 0 0 20px 20px;
		overflow: hidden;
		box-shadow: 0 5px 5px -2px rgba(0, 0, 0, 0.1);

		@media (min-width: 1200px) {
			border-radius: 30px;
			top: 15px;
			padding: 0 5px;
		}
	}

	.select2-search__field,
	.select2-results__option {
		font-size: 14px;
		line-height: 1;
		padding-left: 15px;

		@media (min-width: 1200px) {
			font-size: 16px;
			padding: 12px 20px;
			border-radius: 10px;
		}
	}

	.select2-results__option--highlighted.select2-results__option--selectable {
		background-color: #f8f8f8;
		color: var(--bs-font-color);
	}

	.select2-container--default .select2-results__option--selected {
		background-color: #ececec;
		color: var(--primary-color);
	}

	.select2-results__option--disabled {
		opacity: 0;
		display: none;
	}

	/*
	.select2-dropdown {
		position: absolute;
		width: 100%;
		height: 100dvh;
		top: 0;
		left: 0;
	}

	 */

	& > * {
		max-height: 43px;
	}

}

.main-hero__plate {
	position: relative;
	background-repeat: no-repeat;
	background-position: center right -20px;
	background-size: cover;
	padding: 21px 0 228px;
	border-radius: 20px;
	background-image: url('../img/bg/main-hero.svg');

	@media (min-width: 768px) {
		background-position: center;
	}

	@media (min-width: 992px) {
		//padding: 100px 0 160px;
	}

	@media (min-width: 1200px) {
		border-radius: 40px;
	}

	& > .container {
		position: relative;
		z-index: 2;
	}
}

.main-hero__image {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-repeat: no-repeat;
	background-position: center right;
	border-radius: 20px;
	opacity: 0;

	@media (min-width: 360px) {
		background-size: clamp(327px, 83vw, 429px);
		background-position: top 10px right -56px;
		opacity: 1;
	}

	@media (min-width: 470px) {
		background-position: top 10px right -10px;
	}

	@media (min-width: 768px) {
		background-position: top 20px right 45px;
		background-size: 355px;
	}

	@media (min-width: 992px) {
		background-position: top 20px right 45px;
		background-size: 450px;
	}

	@media (min-width: 1200px) {
		background-position: top 20px right 45px;
		background-size: 480px;
	}

	@media (min-width: 1400px) {
		background-position: top 20px right 45px;
		background-size: 580px;
	}
}