.custom-accordion {
	display: flex;
	flex-direction: column;
	gap: 12px;

	@media (min-width: 992px) {
		gap: 20px;
	}
}

.custom-accordion__item {
	padding: 14px 15px;
	font-weight: 500;
	font-size: 20px;
	color: #383838;
	position: relative;
	overflow: hidden;

	@media (min-width: 992px) {
		padding: 14px 24px;
	}

	&.active {
		.custom-accordion__body {
			opacity: 1;
		}
		.custom-accordion__icon {
			&::before {
				transform: rotate(180deg);
			}

			&::after {
				transform: rotate(180deg);
			}
		}
	}
}

.custom-accordion__head {
	display: flex;
	flex-wrap: nowrap;
	justify-content: space-between;
	align-items: center;
	cursor: pointer;
}

.custom-accordion__title {
	max-width: calc(90% - 16px);
}

.custom-accordion__icon {
	display: block;
	width: 30px;
	height: 30px;
	background-color: var(--primary-color);
	border-radius: 50%;
	position: relative;

	@media (min-width: 992px) {
		width: 48px;
		height: 48px;
	}

	&::before, &::after {
		position: absolute;
		content: '';
		background-color: #fff;
		width: 14px;
		height: 2px;
		transition: all .2s ease;

		@media (min-width: 992px) {
			width: 20px;
			height: 2px;
		}
	}

	&::before {
		top: calc(50% - 1px);
		left: calc(50% - 7px);

		@media (min-width: 992px) {
			top: calc(50% - 1px);
			left: calc(50% - 9px);
		}
	}

	&::after {
		top: calc(50% - 1px);
		left: calc(50% - 7px);
		transform: rotate(90deg);

		@media (min-width: 992px) {
			top: calc(50% - 1px);
			left: calc(50% - 9px);
		}
	}
}

.custom-accordion__wrap {
	display: flex;
	flex-direction: column;
	justify-content: center;
}

.custom-accordion__wrapper,
.custom-accordion__body {
	transition: all .2s ease;
}

.custom-accordion__body {
	font-weight: 400;
	font-size: 16px;
	color: #383838;
	margin-top: 10px;
	opacity: 0;

	p {
		&:last-child {
			margin-bottom: 0;
		}
	}
}


.faq__item {
	&.card {
		box-shadow: none;
		border: none;
	}
}

.faq__item-icon {
	background-color: #fff;
	border: 1px solid #f8f8f8;

	&::before, &::after {
		background-color: #6F6F6F;
	}
}

.faq__item-title {
	font-weight: 400;
	font-size: 16px;
	line-height: 1.4;
	color: var(--bs-font-color);

	@media (min-width: 992px) {
		font-size: 20px;
	}
}

.faq__item-body {
	font-weight: 300;
	font-size: 16px;
	line-height: 1.3;
	color: #383838;

	@media (min-width: 992px) {
		font-size: 18px;
	}
}

.faq__accordion-button {
	margin-top: 20px;
	text-align: center;
}