.recommendation-card {
	color: #000;
	text-decoration: none;
	position: relative;
	padding-bottom: 18px;
	width: 100%;

	&.favorite {
		.recommendation-card__favorite-icon {
			//background-image: url('../img/icons/heart-filled.svg');
		}
	}
	
	

	&:hover {

	}
	
	.product-card__user-elements {
		padding: 5px;
		border-radius: 5px;
		background-color: #fff;
	}
}

.recommendation-card__favorite-icon {
	position: absolute;
	top: 0;
	right: 0;
	width: 35px;
	height: 35px;
	background-image: url('../img/icons/heart.svg');
	background-repeat: no-repeat;
	background-position: center;
	background-size: 20px;
	z-index: 2;
	filter: drop-shadow(0px 0px 7px rgba(0, 0, 0, 0.3));

	&.active {
		background-image: url('../img/icons/heart-filled.svg');
		filter: none;
	}
}

.recommendation-card__image {
	overflow: hidden;
	border-radius: 12px 12px 0 0;

	@media (min-width: 992px) {
		border-radius: 15px 15px 0 0;
	}

	img {
		max-width: 100%;
		max-height: 70vw;
		width: 100%;
		height: 100%;
		object-fit: cover;
		object-position: center;
		transition: all .2s ease;
		transform: scale3d(1,1,1);

		@media (min-width: 768px) {
			max-height: 230px;
		}

		@media (min-width: 992px) {
			max-height: 224px;
		}

		@media (min-width: 992px) {
			max-height: 286px;
		}
	}
}

.recommendation-card__body {
	padding: 12px 12px 0;

	.recommendation-card__title {
		margin-top: 0;
	}
}

.recommendation-card__title {
	margin: 12px 0 7px;
	font-size: 14px;
	color: var(--recommend-link-color);
	transition: all .2s ease;
	font-weight: 600;
	word-wrap: break-word;
	text-overflow: ellipsis;
	display: -webkit-box;
	-webkit-line-clamp: 3;
	-webkit-box-orient: vertical;
	line-height: 1.13em;
	overflow: hidden;
	text-decoration: none;

	@media (min-width: 768px) {
		font-size: 16px;
	}

	&:hover {
		color: var(--primary-color);
	}
}

.recommendation-card__price {
	line-height: 120%;
	color: inherit;
	display: inline-block;
	font-size: 16px;
	font-weight: 600;
	margin: 0 0 9px;
	max-width: 95%;

	@media (min-width: 768px) {
		font-size: 18px;
	}
}

.recommendation-card__location {
	font-size: 12px;
	line-height: 120%;
	color: inherit;

	@media (min-width: 768px) {
		font-size: 14px;
	}
}

.recommendation-card__period {
	//display: none;
	opacity: 0.5;
	margin-top: 5px;
	font-size: 12px;
	color: #222;
	line-height: 120%;

	@media (min-width: 768px) {
		font-size: 14px;
	}
}

.swiper-pagination-bullet {
	background-color: #fff;
	opacity: 0.5;

	&-active {
		opacity: 1;
	}
}