.comment-item {

}

.comment-item__header {
	display: flex;
	flex-wrap: nowrap;
	align-items: center;
	margin-bottom: 10px;
	justify-content: space-between;
}

.comment-item__header--with-info {
	flex-direction: column;
	align-items: flex-start;
}

.comment-item__user {
	display: flex;
	flex-wrap: nowrap;
	align-items: center;
}

.comment-item__info-elements {
	margin-top: 10px;
	display: flex;
	flex-wrap: nowrap;
	gap: 20px;
	justify-content: space-between;
	width: 100%;

	.comment-item__date {
		line-height: 1.2;

		@media (max-width: 767px) {
			margin: 0;
		}
	}

	.comment-item__category{
		margin: 0;
		line-height: 1.2;
	}
}


.comment-item__date {
	font-size: 14px;
	color: #6f6f6f;
	margin-left: 15px;
	margin-right: 15px;

	@media (min-width: 992px) {
		font-size: 16px;
		line-height: 140%;
		margin-left: 20px;
	}

	& > * {
		&:last-child {
			margin-bottom: 0;
		}
	}
}

.comment-item__category {
	font-size: 14px;
	color: #6f6f6f;
	margin-left: 15px;
	margin-right: 15px;

	@media (min-width: 768px) {
		font-size: 16px;
	}

	@media (min-width: 992px) {
		font-size: 18px;
	}
}

.comment-item__user-pic {
	border-radius: 50%;
	overflow: hidden;
	min-width: 68px;
	min-height: 68px;
	max-width: 68px;
	max-height: 68px;

	@media (min-width: 992px) {
		min-width: 90px;
		min-height: 90px;
		max-width: 90px;
		max-height: 90px;
	}

	img {
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
}

.comment-item__user-info {
	margin-left: 15px;
}

.comment-item__profile-title {
	font-weight: 600;
	font-size: 14px;
	margin-bottom: 7.6px;
	line-height: 1.2;

	@media (min-width: 992px) {
		font-size: 20px;
	}
}

.comment-item__profile-rating {
	display: flex;
	flex-wrap: nowrap;
}

.comment-item__profile-rating-value {

}

.comment-item__profile-rating-icon {
	display: flex;
	flex-wrap: nowrap;

	img {
		height: 17px;

		@media (min-width: 992px) {
			height: 22.5px;
		}
	}
}

.comment-item__text {
	[data-block=more-elem] {
		margin-top: 10px;
	}
}

.comment-item__text-title {
	font-weight: 600;
	font-size: 16px;
	color: var(--bs-font-color);
	margin-bottom: 5px;

	@media (min-width: 992px) {
		font-size: 20px;
	}
}

.comment-item__text-buttons {
	display: flex;
	gap: 20px;
	margin-top: 10px;

	& > * {
		margin: 0 !important;
		display: inline-block;
		color: var(--primary-color);
		cursor: pointer;
	}
}

.comment-item__reply-area {
	margin: 10px 0 5px;

	@media (min-width: 768px) {
		margin: 20px 0 10px;
	}

	textarea {
		border-radius: 10px;

		&:focus {
			box-shadow: none;
		}
	}
}

.comment-item__reply-button {
	.btn {
		width: 100%;

		@media (min-width: 768px) {
			max-width: fit-content;
		}
	}
}


.comment-item--answer {
	margin: 15px 0 0 30px;

	@media (min-width: 768px) {
		margin: 20px 0 20px 40px;
	}

	@media (min-width: 992px) {
		margin: 20px 0 20px 60px;
	}

	&:last-child {
		margin-bottom: 0;
	}

	.comment-item__user-pic {
		min-width: 40px;
		min-height: 40px;
		max-width: 40px;
		max-height: 40px;

		@media (min-width: 768px) {
			min-width: 50px;
			min-height: 50px;
			max-width: 50px;
			max-height: 50px;
		}

		@media (min-width: 992px) {
			min-width: 70px;
			min-height: 70px;
			max-width: 70px;
			max-height: 70px;
		}
	}

	.comment-item__profile-title {
		margin: 0;
		line-height: 1.2;
	}

	.comment-item__date {
		margin: 0;
		line-height: 1.2;
	}
}
