.intro-inner + .about-block {
	padding: 40px 0;

	@media (min-width: 992px) {
		padding: 52px 0 82px;
	}
}

.about-block {
	.row {
		&:not(:first-child) {
			margin-top: 40px;

			@media (min-width: 992px) {
				margin-top: 78px;
			}
		}
	}
}

.about-block__image {
	background-repeat: no-repeat;
	background-position: bottom center;
	background-size: contain;
	width: 100%;
	height: 67vw;
	max-height: 294px;

	@media (min-width: 992px) {
		height: 329px;
		max-height: unset;
	}
}

.about-block__descr {
	@media (min-width: 768px) {
		display: flex;
		flex-direction: column;
		justify-content: center;
		height: 100%;
	}
}

.about-block__partners-list {
	list-style: none;
	padding: 0;
	margin: 0;
	display: flex;
	flex-wrap: wrap;
	gap: 20px;
	margin-top: 15px;

	@media (min-width: 768px) {
		margin-top: 0;
	}

	@media (min-width: 992px) {
		align-self: center;
	}
}

.about-block__partners-item {
	width: calc(100% / 3 - 14px);
	overflow: hidden;
	border-radius: 15px;

	@media (min-width: 768px) {
		width: calc(100% / 2 - 10px);
	}

	@media (min-width: 992px) {
		width: calc(100% / 3 - 14px);
	}

	img {
		max-width: 100%;
		height: 100%;
		object-fit: contain;
	}
}