.search-bar {
	background-repeat: no-repeat;
	background-position: center;
	background-image: url('../img/bg/main-hero.svg');
	background-size: 140%;
	padding: 10px 0;

	@media (min-width: 1200px) {
		padding: 20px 0;
	}
}

.search-bar__form {
	display: flex;
	gap: 10px;
	flex-wrap: wrap;
	width: 100%;
	max-width: 100%;

	@media (min-width: 576px) {
		flex-wrap: nowrap;
	}

	.input-group {
		width: 100%;

		@media (min-width: 576px) {
			max-width: unset;
		}
	}

	.select2-container {
		@media (min-width: 375px) {
			width: 245px !important;
		}

		@media (min-width: 400px) {
			width: 270px !important;
		}

		@media (min-width: 430px) {
			width: 300px !important;
		}

		@media (min-width: 475px) {
			width: 345px !important;
		}

		@media (min-width: 520px) {
			width: 380px !important;
		}

		@media (min-width: 576px) {
			width: clamp(345px, 60vw, 576px) !important;
		}

		@media (min-width: 768px) {
			width: clamp(530px, 69vw, 750px) !important;
		}

		@media (min-width: 992px) {
			width: clamp(560px, 76vw, 850px) !important;
		}

		@media (min-width: 1200px) {
			width: clamp(560px, 76vw, 950px) !important;
		}
	}

	.input-group .btn {
		@media (min-width: 375px) {
			width: 100%;
		}


	}
}


.search-bar__form button[type=submit] {
	@media (min-width: 1200px) {
		min-height: 58px;
		min-width: 108px;
		font-size: 16px;
	}
}