.write-us {
	padding-bottom: 0;

	@media (min-width: 768px) {
		padding-top: 40px;
	}

	@media (min-width: 992px) {
		padding-bottom: 60px;
	}

	.write-us__manager-name {
		font-weight: 600;
		line-height: 136%;

		@media (min-width: 992px) {
			font-size: 32px;
		}
	}

	.write-us__manager-position {
		font-size: 16px;
		color: #6f6f6f;
		line-height: 136%;

		@media (min-width: 992px) {
			font-size: 20px;
			line-height: 140%;
		}
	}
}

.write-us__contact {
	@media (min-width: 768px) {
		display: flex;
		flex-direction: column;
		justify-content: center;
		height: 100%;
	}
}

.write-us__manager {
	display: flex;
	flex-wrap: wrap;
	gap: 20px;
	align-content: center;
	margin-bottom: 12px;

	@media (min-width: 992px) {
		margin-bottom: 30px;
	}
}

.write-us__manager-image {
	img {
		width: 60px;
		height: 60px;
		border-radius: 50%;
		overflow: hidden;

		@media (min-width: 992px) {
			width: 109px;
			height: 109px;
		}
	}
}

.write-us__manager-title {
	display: flex;
	flex-direction: column;
	justify-content: center;

	& > * {
		margin: 0;
	}
}



.write-us__text {

}

.write-us__buttons {
	display: flex;
	flex-wrap: wrap;
	gap: 12px;

	@media (min-width: 992px) {
		margin-top: 22px;
		gap: 20px;
	}

	.btn {
		svg {
			width: 20px;
			height: 16.5px;
			margin-right: 6px;
		}
	}
}

.write-us__image {
	margin-top: 40px;
	background-repeat: no-repeat;
	background-position: bottom center;
	background-size: contain;
	width: 100%;
	height: 84vw;
	max-height: 380px;

	@media (min-width: 768px) {
		margin-top: 0;
	}

	@media (min-width: 992px) {
		max-height: 401px;
		margin-top: 60px;
	}
}