$mainOffset: 15; // отступы безопасности для контейнера
$maxWidth: 1920; // максимальная ширина страницы
$minWidth: 320; // минимальная ширина телефона
$containerWidth: 1160; // ширина контейнера из макета
$maxWidthContainer: $containerWidth + ($mainOffset * 2); // ширина контейнера с учетом padding слева и справа

$headerHeight: 100;
$footerHeight: 100;



:root {
  --bs-font-family: MuseoSansCyrl, sans-serif; // Семейство шрифта по умолчанию
  --bs-font-family-second: Inter, sans-serif;
  --bs-font-size: 16px; // Размер шрифта по умолчанию
  --bs-font-weight: 300; // Жирность шрифта по умолчанию
  --bs-line-height: 175%; // Базовая высота шрифта
  --bs-body-bg: #fff;
  --bs-font-color: #222; // Цвет шрифта по умолчанию
  --bs-title-color: #004577; // Цвет заголовков

  @media (min-width: 768px) {
    --bs-font-size: 18px;
  }

  --primary-color: #0069b5;
  --primary-color-hover: #0277cc;

  --card-bg-color: #f8f8f8;

  --footer-bg-color: #fff;
  --footer-text-color: var(--bs-font-color);

  --card-color: #263d6a;
  --recommend-link-color: #0069b5;

  --btn-text: #fff;
  --btn-bg: var(--primary-color);
  --btn-bg-hover: var(--primary-color-hover);


  --primary-color-light: #0069b5;


  --primary-color-light2: #e4f4ff;


  --card-bg-hover: #5c84ec;

  --bs-light-gray: #f1f1f1;
  --bs-bg-gray: #f8f8f8;

  --bs-light-blue: #e6efff;
  --bs-light-secondary: #1bb6e6;


  --bs-success-color: rgb(134, 134, 134);
  --bs-success-color-hover: rgb(0, 171, 17);

  --attention-color: #ff5c16;
  --bs-danger: #ce1414;

  --gray-color: #6F6F6F;
  --gray-color-light: #c5c5c5;

  --scrollbar-color: var(--primary-color);

  --status-success: #5bce80;
  --status-process: #0069b5;
  --status-in-work: #088c15;
  --status-denial: #cc4855;
  --status-cancel: #757575;
  --status-closed: #ef9e00;
}