.lk-my-announcements {
	.product-content__cards-list {
		&.table-view {
			gap: 30px;

			@media (min-width: 576px) and (max-width: 767px) {
				gap: 10px;
			}

			@media (min-width: 768px) {
				padding-bottom: 0;
			}

			.product-content__cards-item {
				min-height: 255px;

				@media (min-width: 576px) and (max-width: 767px) {
					width: calc(100% / 2 - 5px);
				}
			}

			.product-card__info {
				@media (max-width: 767px) {
					margin-top: 10px;
				}
			}

			.product-card__price-overview {
				@media (max-width: 767px) {
					font-size: 18px;
					line-height: 1.2;
				}
			}

			.product-card__card-statistic {
				@media (max-width: 767px) {
					margin-top: 10px;
					margin-bottom: 5px;
					padding: 10px;
					flex-direction: row;
				}

				@media (max-width: 575px) {
					flex-direction: column;
				}
			}

			.product-card__text {
				-webkit-line-clamp: 3;

				@media (max-width: 767px) {
					display: none;
				}
			}
		}
	}

	.product-card__prices {
		margin-top: 10px;

		@media (min-width: 768px) {
			margin-top: 0;
		}
	}

	.product-card__price-overview {
		@media (min-width: 768px) {
			margin-bottom: 16px;
		}
	}
}