.floating-button {
	transition: opacity 0.15s ease-out, transform 0.2s ease-out;
	position: fixed;
	transform: translateX(-50%) translateY(0px);
	bottom: 10px;
	left: 50%;
	width: max-content;

	&.show {
		transform: translateX(-50%) translateY(0);
		z-index: 40;
		opacity: 1;
	}

	&.hide {
		opacity: 0;
		transform: translateX(-50%) translateY(20px);
	}
}

@keyframes slide-up {
	from {
		opacity: 0;
		transform: translateX(-50%) translateY(20px);
	}
	to {
		opacity: 1;
		transform: translateX(-50%) translateY(0);
	}
}

@keyframes slide-down {
	from {
		opacity: 1;
		transform: translateX(-50%) translateY(0);
	}
	to {
		opacity: 0;
		transform: translateX(-50%) translateY(20px);
	}
}

/**
* Reinstate scrolling for non-JS clients
*/

.simplebar-content-wrapper {
	scrollbar-width: auto;
	-ms-overflow-style: auto;
}

.simplebar-content-wrapper::-webkit-scrollbar,
.simplebar-hide-scrollbar::-webkit-scrollbar {
	display: initial;
	width: initial;
	height: initial;
}

.select2-results__options {
	scrollbar-width: thin; /* Firefox */
	scrollbar-color: transparent transparent; /* Firefox */
}

.select2-results__options::-webkit-scrollbar {
	width: 0px; /* Chrome/Safari */
	height: 0px;
}

.simplebar-scrollbar {
	right: -2px;
}

.simplebar-track.simplebar-vertical .simplebar-scrollbar:before {
	background-color: var(--scrollbar-color);
}


.simplebar-scrollbar.simplebar-hover:before,
.simplebar-scrollbar.simplebar-visible:before {
	opacity: 0;

	@media (min-width: 992px) {
		opacity: 1;
	}
}

.mobile-simplebar-track {
	.simplebar-scrollbar.simplebar-hover:before,
	.simplebar-scrollbar.simplebar-visible:before {
		opacity: 1;
	}
}

.page-full-map {
	.product-sidebar__services {
		margin-top: 20px;
	}
}

.mobile-block {
	@media (min-width: 1200px) {
		display: none;
	}
}


[data-tabs="tabs-panels"] >  *,
.tabs-block  .tab-panel {
	display: none;
}

[data-tabs="tabs-panels"] >  *.active,
.tabs-block  .tab-panel.active {
	display: block;
}

[data-tabs="tabs-panels"] {

	.dimmed {
		.recommendation-card,
		.product-card,
		.card {
			opacity: 0.6;
		}
	}

	.drafts {
		.card {
			box-shadow: none;
			border-color: var(--gray-color-light);
			background-color: var(--card-bg-color);
		}

		img {
			opacity: 0.5;
		}
	}

	.responded {
		.card {
			border-color: #78b2da;
			background-color: #e9f6ff;
			box-shadow: none;
		}
	}

	.invitation {
		.card {
			border-color: #7fc762;
			background-color: #f0ffea;
			box-shadow: none;
		}
	}
}


[data-tabs="tabs-list"] {
	--bs-gutter-x: 2rem;

	padding: 0;
	margin: 0;
	list-style: none;
	display: inline-flex;
	border-bottom: 1px solid  #d0d0d0;
	//max-width: 390px;
	margin-bottom: 15px;


	@media (max-width: 767px) {
		display: flex;
	}

	@media (min-width: 768px) {
		width: fit-content;
		left: 0;
		padding-left: 0 !important;
		padding-right: 0 !important;
	}

	@media (min-width: 992px) {
		margin-bottom: 30px;
	}

	& > * {
		flex-basis: 50%;
	}

	.tabs__wrap {
		display: flex;
		flex-wrap: nowrap;
	}

	li {
		font-weight: 600;
		color: #d0d0d0;
		min-width: fit-content;
		//padding-left: 20px;
		padding: 0 10px 8px;
		font-size: 20px;
		flex-basis: auto;

		@media (min-width: 768px) {

		}

		&.active {
			color: #0069b5;
			border-bottom: 2px solid  #0069b5;
		}
	}
}


.tabs-pills[data-tabs=tabs-list] {
	border: none;

	.tabs-pills__wrap {
		//flex-basis: 100%;
		border: 1px solid #d0d0d0;
		border-radius: 100px;
		background-color: #fff;

		& > * {
			border-radius: 59px;
			padding: 14px 20px;
			border: none;
			font-weight: 400;
			font-size: 16px;
			color: #2a2a2a;
			//width: fit-content;
			white-space: nowrap;

			&.active {
				background: #535353;
				color: #fff;
			}
		}
	}
}



//----------------------//

[data-block="less-more"] {
	position: relative;
	overflow: hidden;
}
[data-block="more-elem"] {
	display: block;
	color: var(--primary-color);
	cursor: pointer;
	margin-top: 20px;

	&.collapsed {
		display: none;
	}
}

[data-block="content"] {
	display: -webkit-box;
	-webkit-box-orient: vertical;
	overflow: hidden;

	& > *:last-child {
		margin-bottom: 1px;
	}
}

.modal__text {
	.text-item__definition {
		font-weight: 600;
		font-size: 20px;

		@media (min-width: 992px) {
			font-size: 24px;
		}
	}

	ul {
		li {
			font-weight: 400;
			font-size: 16px;

			@media (min-width: 992px) {
				font-size: 20px;
			}
		}
	}
}

