.lk-favorites {
	.product-card {
		.product-card__image-slider {

		}
	}

	.profile-card {
		.product-card__user-elements {
			@media (max-width: 767px) {
				top: 10px;
				right: 10px;
			}
		}
		.announcementsSlider {
			padding-bottom: 40px;
			border-radius: 10px;

			& > .swiper-wrapper {


				& + .swiper-pagination {
					.swiper-pagination-bullet {
						background-color: #bdbdbd;
					}

					.swiper-pagination-bullet-active {
						background-color: var(--primary-color);
					}
				}
			}
		}



		.profile-avatar-and-name {
			margin-bottom: 20px;

			a {
				text-decoration: none;
			}
		}

		.favorite-icon {
			background-size: 18px;
			filter: none;
		}

		.profile-avatar-and-name__pic {
			width: 66px;
			height: 66px;
		}

		.profile-avatar-and-name__info {
			display: flex;
			flex-direction: column;
		}

		.profile-avatar-and-name__name {
			font-weight: 600;
			line-height: 1.4;
		}

		.profile-avatar-and-name__announcements {
			font-weight: 400;
			font-size: 16px;
			color: var(--gray-color);
			line-height: 1.4;
		}

		.recommendation__item {
			width: 100%;

			.profile-card__slider {
				.swiper-slide img {
					//height: 77vw;
				}
			}
		}
	}
}