.lk-profile {
	.product-content__sidebar-temp {
		@media (min-width: 1200px) {
			top: 111px;
		}
	}

	.section-title {
		@media (max-width: 767px) {
			font-size: 24px;
		}
	}
}

.profile-user-navigation {
	ul {
		li {
		}
	}
}

.profile-user-navigation__link {
	display: block;
	border-radius: 10px;
	padding: 14px 20px;
	text-decoration: none;
	font-weight: 400;
	font-size: 20px;
	color: var(--bs-font-color);
	transition: all .2s ease;

	&:hover {
		color: var(--primary-color);
	}

	&.active {
		background-color: #fff;
		pointer-events: none;
	}
}

.user-card {
	.product-card__user-elements {
		@media (max-width: 991px) {
			background-color: transparent;
		}
	}

	.product-card__user-elements {
		top: 10px;
		right: 10px;

		i {
			font-size: 25px;

			@media (min-width: 768px) {
				font-size: 30px;
			}
		}
	}
}

.lk-profile__section {
	@media (max-width: 991px) {
		padding: 0;
		background-color: transparent;
		box-shadow: none;
	}

	&:not(:last-of-type) {
		margin-bottom: 35px;

		@media (min-width: 992px) {
			margin-bottom: 40px;
		}
	}
}

.user-card__profile {
	@media (min-width: 650px) {
		display: flex;
		gap: 20px;
	}
}

.user-card__image {
	float: left;
	width: 100px;
	margin-right: 12px;
	margin-bottom: 20px;

	@media (min-width: 440px) {
		width: 22vw;
		height: 145px;
		overflow: hidden;
		border-radius: 5px;
		margin-bottom: 0;
		margin-right: 15px;
	}

	@media (min-width: 450px) {
		width: 26vw;
	}

	@media (min-width: 576px) {
		width: 145px;
		margin-right: 18px;
	}

	@media (min-width: 650px) {
		float: none;
		margin: 0;
	}

	@media (min-width: 992px) {
		width: 178px;
		height: 178px;
	}

	img {
		max-width: 100%;
		height: 100%;
		object-fit: cover;
	}
}

.user-card__profile-title {
	font-weight: 600;
	font-size: 20px;
	line-height: 1;
	margin-bottom: 8px;
	max-width: 90%;

	@media (min-width: 650px) {
		font-size: 26px;
	}

	@media (min-width: 768px) {
		font-size: 32px;
	}

	@media (min-width: 992px) {
		margin-bottom: 16px;
	}
}

.rating-element {
	display: flex;
	align-items: center;
	gap: 5px;
	margin-bottom: 10px;

	@media (min-width: 440px) {
		margin-bottom: 15px;
	}

	@media (min-width: 650px) {
		gap: 10px;
	}

	@media (min-width: 992px) {
		margin-bottom: 30px;
	}
}

.rating-element__value {
	font-weight: 600;
	font-size: 16px;

	@media (min-width: 440px) {
		font-size: 20px;
	}

	@media (min-width: 768px) {
		font-size: 24px;
	}
}

.rating-element__icons {
	display: flex;
	flex-wrap: nowrap;

	img {
		width: 20px;
		height: 20px;

		@media (min-width: 440px) {
			width: 25px;
			height: 25px;
		}

		@media (min-width: 768px) {
			width: 30px;
			height: 30px;
		}
	}
}

.user-card__city {
	font-weight: 400;
	display: flex;
	width: 100%;
	font-size: 16px;
	color: #6f6f6f;
	line-height: 1;
	gap: 5px;
	margin-bottom: 10px;

	@media (min-width: 440px) {
		display: block;
	}

	@media (min-width: 650px) {
		margin-bottom: 15px;
	}

	@media (min-width: 768px) {
		font-size: 18px;
	}

	@media (min-width: 992px) {
		font-size: 20px;
		margin-bottom: 22px;
	}

	i {
		position: relative;
		top: 1px;
	}
}

.user-card__contacts-elements {
	@media (min-width: 650px) {
		display: flex;
		gap: 20px;
	}

	@media (min-width: 768px) {
		gap: 30px;
	}
}

.user-card__link {
	display: block;
	font-weight: 400;
	color: var(--bs-font-color);
	text-decoration: none;
	line-height: 1;
	margin-bottom: 10px;

	&:hover {
		color: var(--primary-color);
	}

	@media (min-width: 440px) {
		display: block;
	}

	@media (min-width: 650px) {
		margin-bottom: 0;
	}

}

.user-card__contacts {
	font-weight: 400;
	font-size: 16px;
	color: var(--bs-font-color);
	text-decoration: none;
	line-height: 1;

	& > *:last-child {
		margin-bottom: 0;
	}

	@media (min-width: 768px) {
		font-size: 18px;
	}

	@media (min-width: 992px) {
		font-size: 20px;
	}
}

.user-card__schedule {
	@media (min-width: 440px) {
		display: block;
	}
}

.user-card__about {
	margin-top: 20px;

	@media (min-width: 992px) {
		margin-top: 30px;
	}
}

.user-card__title {
	margin-bottom: 16px;
	font-size: 20px;

	@media (min-width: 768px) {
		font-size: 24px;
	}

	@media (min-width: 992px) {
		font-size: 32px;
	}
}

.lk-profile__section {
	box-shadow: none;

	&.border {
		border: 1px solid #eee;
	}

	[data-tabs="tabs-list"] {
		@media (min-width: 992px) {
			width: 100%;
		}
	}

	.partner-profile__cards-list {
		display: flex;
		gap: 10px;
		flex-wrap: wrap;

		@media (min-width: 992px) {
			gap: 20px;
		}
	}

	.partner-profile__cards-item {
		width: calc(100% / 2 - 5px);

		@media (min-width: 768px) {
			width: calc(100% / 3 - 7px);
		}

		@media (min-width: 992px) {
			width: calc(100% / 3 - 14px);
		}

		.product-card__user-elements {
			top: 10px;
			right: 10px;
		}
	}
}

.lk-profile__section-rating {
	@media (min-width: 576px) {
		display: flex;
		flex-wrap: nowrap;
		gap: 20px;
		justify-content: space-between;
	}
}

.lk-settings-form {
	a {
		text-decoration: none;
		color: var(--bs-font-color);

		&:hover {
			color: var(--primary-color);
		}
	}

	.editor-form__title {
		@media (min-width: 992px) {
			font-size: 24px;
		}
	}
}

.lk-profile__section-with-subtitle {
	.editor-form__title {
		margin-bottom: 10px;

		@media (min-width: 992px) {
			font-size: 30px;
		}
	}

	.editor-form__sub-title {

	}
}

.lk-profile__documents-list {
	display: flex;
	flex-direction: column;
	gap: 20px;

	@media (min-width: 992px) {
		gap: 30px;
	}
}

.lk-profile__tabs {
	margin-top: 30px;

	@media (min-width: 768px) {
		margin-top: 20px;
	}

	@media (min-width: 992px) {
		margin-top: 0;
	}
}

.document-item {
	display: flex;
	flex-direction: row;
	flex-wrap: nowrap;
	gap: 20px;
	justify-content: space-between;
	align-items: center;
	border-radius: 10px;

	@media (min-width: 768px) {
		border-radius: 20px;
	}
}

.document-item__name {
	display: flex;
	flex-direction: row;
	gap: 10px;
	align-items: center;
	max-width: 70%;

	@media (min-width: 768px) {
		gap: 15px;
	}
}

.document-item__title {
	font-weight: 400;
	font-size: 16px;
	line-height: 1.4;
	color: var(--bs-font-color);

	@media (min-width: 992px) {
		font-size: 20px;
	}
}

.document-item__buttons {
	display: flex;
	flex-wrap: wrap;
	gap: 10px;
	justify-content: flex-end;
	min-width: 66px;
}

.circle-icon-button {
	border: 1px solid #eee;
	border-radius: 50%;
	width: 28px;
	height: 28px;
	padding: 6px;
	text-align: center;
	text-decoration: none;
	display: inline-flex;
	align-items: center;
	justify-content: center;
	transition: all .2s ease;

	@media (min-width: 768px) {
		width: 48px;
		height: 48px;
		padding: 10px;
	}

	i {
		font-size: 16px;
		color: var(--gray-color);
		transition: all .2s ease;

		@media (min-width: 768px) {
			font-size: 26px;
		}

		&.icon-download {
			color: var(--primary-color);
		}
	}

	&:hover {
		background-color: var(--primary-color);
		border-color: var(--primary-color);

		i	{
			color: #fff;
		}
	}

}

.lk-profile__tariff-cards,
.lk-profile__balance-cards {
	display: flex;
	flex-wrap: wrap;
	flex-direction: column;
	gap: 20px;
	flex-grow: 1;

	@media (min-width: 768px) {
		flex-direction: row;
	}
}

.tariff-cards__item {
	@media (min-width: 768px) {
		width: calc(100% / 2 - 10px);
	}

	@media (min-width: 992px) {
		width: calc(100% / 3 - 14px);
	}
}

.tariff-card {
	padding: 20px;
	color: var(--bs-font-color);
	box-shadow: none;
	border: 1px solid #d0d0d0;
}

.tariff-card__type {
	border-radius: 50px;
	padding: 8px 12px;
	background: #eee;
	width: fit-content;
	display: inline-block;
	margin-bottom: 20px;
	font-size: 14px;
	line-height:1;
	font-weight: 400;
}

.tariff-card__payment {
	display: flex;
	flex-direction: column;
	margin-bottom: 20px;
}

.tariff-card__price {
	font-weight: 600;
	font-size: 32px;
	font-family: Montserrat, sans-serif;
}

.tariff-card__rate {
	font-weight: 400;
	font-size: 16px;
}

.tariff-card__description {
	margin-top: 20px;

	p {
		line-height: 1.2;

		&:last-of-type {
			margin-bottom: 0;
		}
	}

	b {
		font-weight: 600;
		font-size: 16px;
		color: #222;
	}

	ul {
		display: flex;
		flex-direction: column;
		gap: 12px;

		&:last-of-type {
			margin-bottom: 0;
		}

		li {
			display: flex;
			//align-items: center;
			flex-wrap: nowrap;
			font-size: 14px;
			font-weight: 400;
			line-height: 1.2;
		}

		i {
			width: 18px;
			height: 18px;
			margin-right: 10px;
			flex-shrink: 0;
			position: relative;
			top: -1px;
		}
	}
}

.rounded-icon {
	display: inline-flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	background-color: var(--primary-color);
	color: #fff;
	border-radius: 50%;
	font-size: 10px;
}

.lk-profile__balance-blocks {
	display: flex;
	flex-wrap: wrap;
	gap: 30px;

	@media (min-width: 992px) {
		gap: 70px;
	}
}

.balance-card {
	@media (min-width: 768px) {
		padding: 30px;
	}
}

.lk-profile__balance-card {
	height: fit-content;
	padding: 20px;

	&--balance {
		@media (min-width: 992px) {
			width: calc(45% - 5px);
		}

		.balance-card__buttons {
			.btn {
				@media (min-width: 1200px) {
					padding: 12.5px 13px;
				}

				@media (min-width: 1400px) {
					padding: 12.5px 15px;
				}
			}
		}
	}

	&--requisites {
		@media (min-width: 992px) {
			width: calc(53% + 4px);
		}

		@media (min-width: 1200px) {
			width: calc(52% + 11px);
		}

		@media (min-width: 1400px) {
			width: calc(52% + 12px);
		}
	}
}

.balance-card__title {
	font-weight: 600;
	font-size: 24px;
	margin-bottom: 18px;
}

.balance-card__balance {
	font-weight: 600;
	font-size: 36px;
	font-family: Montserrat, sans-serif;
}

.balance-card__buttons {
	margin-top: 40px;
	display: flex;
	flex-wrap: wrap;
	gap: 10px;

	@media (min-width: 1200px) {
		justify-content: space-between;
	}

	& > * {
		@media (max-width: 449px) {
			width: 100%;
		}
	}
}

.withdrawal-method__requisites {
	display: flex;
	align-items: center;
	flex-wrap: wrap;
	gap: 8px;

	@media (max-width: 575px) {
		width: 100%;
	}

	@media (min-width: 1200px) {
		max-width: 238px;
	}

	i {
		display: inline-block;
		width: 24px;
		height: 24px;
		background-repeat: no-repeat;
		background-position: center;
		background-size: contain;
	}

	span {
		font-weight: 400;
		font-size: 22px;
	}
}

.balance-card__withdrawal-methods {
	display: flex;
	flex-direction: column;
	gap: 22px;
}

.withdrawal-method {
	display: flex;
	//gap: 10px;
	flex-wrap: wrap;

	@media (min-width: 450px) {
		gap: 10px;
	}

	@media (min-width: 576px) {
		column-gap: 30px;
		row-gap: 10px;
	}
}

.withdrawal-method__name {
	font-weight: 400;
	font-size: 16px;
	line-height: 1.2;
	margin-bottom: 10px;
	width: 100%;

	@media (min-width: 450px) {
		margin-bottom: 0;
	}
}

.withdrawal-icon {

	&--sber {
		background-image: url('../img/icons/sber.png');
	}
}

.withdrawal-method__controls {
	margin-top: 5px;
	display: flex;
	gap: 16px;

	@media (min-width: 450px) {
		margin-top: 0;
	}
}

.withdrawal-method__control-link {
	color: var(--bs-font-color);
	line-height: 1.2;
	font-weight: 400;
	font-size: 16px;

	&:hover {
		color: var(--primary-color);
	}
}

.lk-profile__balance-history {
	width: 100%;
}

.lk-profile__balance-history-item {
	&:first-child {
		padding-top: 0;
	}
}

.balance-history-item {
	padding: 20px 0;
}

.balance-history-item__date,
.balance-history-item__transaction-value {
	margin-bottom: 5px;

	@media (min-width: 576px) {
		margin-bottom: 0;
	}
}


.balance-history-item__date {
	font-weight: 400;
	font-size: 14px;
	line-height: 1.2;

	@media (min-width: 576px) {
		display: flex;
		flex-direction: column;
	}
}

.balance-history-item__transaction {
	display: flex;
	flex-wrap: nowrap;
	gap: 10px;
	margin-bottom: 10px;
	align-items: center;
	justify-content: space-between;

	@media (min-width: 450px) {
		gap: 20px;
	}

	@media (min-width: 576px) {
		margin-bottom: 0;
	}
}

.balance-history-item__transaction-description {
	font-weight: 400;
	font-size: 14px;
	color: #000;
	line-height: 1.2;
}

.balance-history-item__transaction-value {
	font-weight: 700;
	font-size: 16px;
	font-family: Montserrat, sans-serif;
	margin-bottom: 0;
	min-width: max-content;

	@media (min-width: 992px) {
		font-size: 20px;
	}
}

.balance-history-item__status {
	@media (min-width: 576px) {
		display: flex;
		flex-direction: column;
		justify-content: center;
		height: 100%;
		align-items: flex-end;
	}
}

.status-tip {
	border-radius: 4px;
	padding: 4px 8px;
	background-color: #000;
	color: #fff;
	font-weight: 600;
	font-size: 16px;
	height: min-content;
	line-height: 1.2;

	&--process {
		background-color: var(--status-process);
	}

	&--success {
		background-color: var(--status-success);
	}

	&--denial {
		background-color: var(--status-denial);
	}

	&--cancel {
		background-color: var(--status-cancel);
	}
}

.lk-profile__balance-pagination {
	margin-top: 30px;
}