.header {
  width: 100%;
  transition: background-color var(--bs-transition);
  z-index: 50;

  .navbar {
    padding: 6px 0;
  }

  &.header-is-fixed {
    position: fixed;
    visibility: visible !important;
    background-color: #fff;
    transition: all .15s ease;

    &.scroll {
      box-shadow: 0 1px 9px -4px rgba(0, 0, 0, 0.5);
      transition: all .3s ease;
    }

    & + main {
      padding-top: 50px;

      @media (min-width: 768px) {
        //padding-top: 62px;
      }

      @media (min-width: 992px) {
        padding-top: 70px;
      }
    }
  }

  .navbar__content {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    align-items: center;
  
    @media (min-width: 992px) {
      width: fit-content;
    }
  
    @media (min-width: 1200px) {
      margin-right: 20px;
    }
  }
}

.navbar-brand {
  width: 140px;
  margin-right: 0;
  padding: 0;

  @media (min-width: 992px) {
    width: 200px;
  }

  @media (min-width: 1200px) {
    width: 204px;
  }

  img, svg {
    max-width: 100%;
  }

  &--crm {
    width: 92px;
    margin-left: 12px;

    @media (min-width: 480px) {
      margin-left: 20px;
    }

    @media (min-width: 992px) {
      width: 138px;
      margin-left: 12px;
    }

    @media (min-width: 1200px) {
      margin-left: 20px;
    }
  }
}

.header .navbar-toggler {
  margin-left: auto;
}
  // Burger
.navbar-toggler {
  border: none !important;
  padding: 10px;
  position: relative;
  right: -10px;

  &:focus {
    border: none !important;
    box-shadow: none;
  }

  .toggler-icon {
    width: 26px;
    height: 2px;
    border-radius: 4px;
    background-color: var(--primary-color);
    background-color: #1db5e6;
    display: block;
    transition: all 0.2s;
    position: relative;
  }

  .top-bar {
    transform: rotate(0);
  }

  .middle-bar {
    opacity: 1;
    filter: alpha(opacity=100);
    margin: 6px auto;
  }

  .bottom-bar {
    transform: rotate(0);
  }
}

.navbar-toggler {
  &[aria-expanded="true"] {
    .toggler-icon {
      //background-color: #fff;
      //width: 37px;
    }

    .top-bar {
      transform: translateX(25%) rotate(45deg);
      transform-origin: 10% 10%;
      top: 2.5px;
    }

    .middle-bar {
      opacity: 0;
      filter: alpha(opacity=0);
    }

    .bottom-bar {
      transform: translateX(25%) rotate(-45deg);
      transform-origin: 10% 90%;
      bottom: 2.5px;
    }
  }
}

.header {
  .navbar {
    @media (min-width: 992px) {
      padding-top: 10px;
      padding-bottom: 10px;
    }
  }

  .offcanvas-header {
    padding-top: 10px;
    padding-bottom: 10px;
  }

  .offcanvas-body {
    border-top: 1px solid #e9e9e9;
  }

  .offcanvas {
    .btn-close {
      background-color: #f1f1f1;
      opacity: 1;
      border-radius: 50%;
      background-image: none;
      display: flex;
      align-items: center;
      margin-right: 0;

      .icon-cross {
        color: #b6b5b5;
      }
    }

    .nav__menu {
      li {
        border: none;
      }

      a {
        display: block;
        font-weight: bold;
      }
    }

    .btn {
      width: 100%;
    }
  }

  .nav__menu {
    @media (min-width: 992px) {
      display: flex;
      justify-content: space-evenly;
      flex-grow: 1;
    }
    @media (min-width: 1200px) {
      justify-content: center;
    }

    a {
      color: #3c4c53;
      text-decoration: none;

      @media (min-width: 992px) {
        font-size: 16px;
        font-weight: 400;
      }

      &:hover {
        @media (min-width: 992px) {
          color: var(--primary-color);
        }
      }
    }

    li {
      &:hover {
        @media (max-width: 991px) {
          border-radius: 30px;
          background-color: var(--bs-light-gray);
        }
      }
    }

    i {
      margin-right: 5px;
      position: relative;
      top: 1px;
    }
  }

  .navbar-brand svg {
    @media (min-width: 992px) {
      position: relative;
      top: -2px;
    }
  }
}

.header__btn {
  @media (min-width: 992px) {
    min-width: 160px;
  }

  @media (min-width: 1200px) {
    margin-left: 20px;
    min-width: 198px;
  }
}

header {
  .user-nav-elements {
    display: flex;
    flex-wrap: nowrap;
    gap: 8px;
    align-items: center;
    height: 38px;

    @media (min-width: 350px) {
      gap: 10px;
    }

    @media (min-width: 480px) {
      gap: 16px;
    }

    @media (min-width: 768px) {
      height: 56px;
    }

    a {
      text-decoration: none;
    }

    i {
      color: var(--gray-color);
      font-size: 30px;
    }

    .dropdown-toggle {
      &::after {
        content: none;
      }
    }

    .dropdown-menu {
      margin-top: 6px;
      //border-color: #ededed;
      border-color: transparent;
      padding: 20px;
      box-shadow: 0 6px 13px -9px rgba(0, 0, 0, 0.5);

      @media (min-width: 992px) {
        margin-top: 9px;
      }

      &.show {
        border-radius: 0 0 20px 20px;
      }
    }
  }

  .user-nav-elements__notification {
    position: relative;
    height: 100%;

    @media (max-width: 767px) {
      top: 2px;
    }

    & > * {
      height: 100%;
    }

    .dropdown-toggle {
      display: flex;
      align-items: center;
    }

    .dropdown-menu {
      //margin-top: 19px;
      padding: 10px;
    }
  }


  .user-nav-elements__notification-count {
    position: absolute;
    top: 0;
    right: 0;
    display: flex;
    border-radius: 50%;
    padding: 3px;
    width: 14px ;
    height: 14px ;
    background: var(--attention-color);
    color: #fff;
    font-weight: 600;
    font-size: 9px;
    line-height: 1;
    text-transform: uppercase;
    text-align: center;
    font-family: Montserrat, sans-serif;
    justify-content: center;
    align-items: flex-end;
    user-select: none;
  }

  .user-nav-elements__notification-item {
    background-color: var(--bs-light-gray);
    //color: #fff;
    padding: 10px 15px 10px 10px;
    font-size: 14px;
    margin-bottom: 10px;
    border-radius: 10px;
    line-height: 1.2;
    width: 200px;
    position: relative;

    &:last-child {
      margin-bottom: 0;
    }

    & > * {
      &:last-child {
        margin-bottom: 0;
      }
    }

    i {
      position: absolute;
      top: 8px;
      right: 8px;
      font-size: 10px;
      cursor: pointer;
      pointer-events: none;
      transition: all .1s ease;
      color: var(--primary-color);

      @media (min-width: 992px) {
        opacity: 0;
      }

      &:hover {
        color: var(--bs-danger);
      }
    }

    &:hover {
      i {
        opacity: 1;
        pointer-events: auto;
      }
    }
  }

  .user-nav-elements__notification-item-content {
    & > * {
      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  .user-nav-elements__notification-reset-all {
    text-align: center;
    font-size: 14px;
    display: block;
    text-decoration: none;
    cursor: pointer;
    line-height: 1.2;
    color: var(--primary-color);
    transition: all .1s ease;

    &:hover {
      color: var(--bs-danger);
    }
  }

  .user-nav-elements__wallet {
    display: flex;
    gap: 8px;
    flex-wrap: nowrap;

    span {
      font-weight: 600;
      font-size: 15px;
      color: #222;
      font-family: Montserrat, sans-serif;
    }
  }

  .user-nav-elements__profile {
    display: flex;
    flex-wrap: nowrap;

    .btn-group {
      align-items: center;
    }

    .dropdown-toggle {
      display: flex;
      flex-wrap: nowrap;
      align-items: center;
      gap: 10px;
      cursor: pointer;
      padding: 0;

      i {
        font-size: 16px;
        color: var(--primary-color);
        display: inline-block;
        transform: rotate(270deg);
        transition: all .1s ease;
      }

      &.show {
        i {
          transform: rotate(90deg);
        }
      }
    }

    .dropdown-menu__user-info {
      //left: 36px;

      ul {
        margin: 0;
        padding: 0;
        list-style: none;

        li {
          &:not(:last-child) {
            margin-bottom: 14px;
          }
        }

        a {
          font-weight: 400;
          font-size: 16px;
          color: var(--bs-font-color);
          display: block;
          line-height: 1.4;

          &:hover {
            color: var(--primary-color);
          }
        }
      }
    }
  }

  .user-nav-elements__user-photo {

    img {
      width: 56px;
      height: 56px;
      border-radius: 50%;
      object-fit: cover;
      object-position: center;
    }
  }

  .user-nav-elements__user-info {
    margin-bottom: 20px;

    .btn {
      margin-top: 10px;
      width: 100%;
      max-width: 210px;
    }
  }

  .user-nav-elements__user-name {
    font-weight: 600;
    font-size: 20px;
    width: 243px;
    margin-bottom: 0;
    line-height: 1;
  }

  .user-nav-elements__user-type {
    font-weight: 600;
    font-size: 16px;
    color: var(--gray-color);
  }

  .user-nav-elements__log-out {
    margin-top: 20px;
    font-weight: 600;
    font-size: 16px;
    color: var(--primary-color);

    a {
      color: inherit;

      &:hover {
        color: var(--btn-bg-hover);
      }
    }
  }
}

.header--lk {
  .navbar__content {
    align-items: baseline;

    @media (min-width: 992px) {
      align-items: center;
    }
  }

  .navbar-brand {
    width: 110px;
    line-height: 1;

    @media (min-width: 350px) {
      width: 126px;
    }

    @media (min-width: 992px) {
      width: 145px;
    }

    @media (min-width: 1200px) {
      width: 204px;
    }
  }

  .navbar-brand--crm {
    width: 70px;
    position: relative;
    top: -1px;

    @media (min-width: 350px) {
      width: 92px;
    }

    @media (min-width: 1200px) {
      width: 138px;
    }
  }

  .nav__menu {
    display: none;

    @media (min-width: 992px) {
      display: flex;
      justify-content: center;
    }

    li {

    }

    a {
      @media (min-width: 992px) {
        font-size: 14px;
      }



      @media (min-width: 1200px) {
        font-size: 16px;
      }
    }
  }

  .header__btn {
    display: none;

    @media (min-width: 768px) {
      display: inline-block;
      min-width: 170px;
      margin-left: 10px;
    }
  }

  .user-nav-elements__wallet {
    display: none;

    @media (min-width: 480px) {
      display: flex;
    }
  }

  .user-nav-elements__user-photo {
    img {
      @media (max-width: 767px) {
        width: 30px;
        height: 30px;
      }
    }
  }

  .user-nav-elements__notification {
    .dropdown-menu {
      @media (max-width: 479px) {
        left: auto;
        right: -56px;
      }

      @media (min-width: 480px) {
        left: calc(50% - 110px);
        right: auto;
      }
    }
  }

  .user-nav-elements {
    .dropdown-menu {
      @media (max-width: 767px) {
        margin-top: 4px;
      }
    }
  }

  .user-nav-elements__profile {
    .dropdown-toggle i {
      display: none;

      @media (min-width: 768px) {
        display: inline-block;
      }
    }

    .dropdown-menu {
      left: auto;
      right: -16px;

      @media (max-width: 767px) {
        margin-top: 10px;
      }

      @media (min-width: 768px) {
        left: calc(50% - 63px);
        right: auto;
      }
    }
  }
}
