.product-content {
	padding: 0;

	@media (max-width: 1199px) {
		overflow: hidden;
	}

	.main-hero__form {
		max-width: unset;
	}

	.announcement-map__wrap {
		width: 100%;
		max-width: 100%;
		padding: 0;
		margin-bottom: 10px;

		@media (min-width: 768px) {
			margin-bottom: 0;
		}
	}
}

.product-content__wrap {
	display: flex;
	gap: 40px;
}

.product-content__sidebar-temp {
	margin-bottom: 15px;

	@media (min-width: 1200px) {
		margin-bottom: 0;
		overflow-y: scroll;
		max-height: calc(100vh - 100px);
		position: sticky;
		top: calc(72px + 30px);


		&.sticky-bottom {
			display: flex;

			.product-content__sidebar {
				position: sticky;
				top: auto;
				bottom: 0;
				//align-self: flex-end;
				margin-top: auto;
			}
		}
	}
}

/* Скрываем scrollbar для Chrome, Safari и Opera */
.product-content__sidebar-temp::-webkit-scrollbar {
	display: none;
}

/* Скрываем scrollbar для IE, Edge и Firefox */
.product-content__sidebar-temp {
	-ms-overflow-style: none;  /* IE и Edge */
	scrollbar-width: none;  /* Firefox */
}

.product-content__col-sidebar {
	@media (min-width: 1200px) {
		width: 23%;
	}
}

.product-content__col-content {
	@media (min-width: 1200px) {
		width: 77%;
	}
}



.product-sidebar__filter {
	@media (max-width: 1199px) {
		height: calc(100dvh - 72px);
		overflow-y: scroll;
		display: flex;
		flex-direction: column;
		overflow-x: hidden;
		padding-right: calc(var(--bs-gutter-x)* .5);
		padding-left: calc(var(--bs-gutter-x)* .5);
	}
}


.mobile-modal-element {
	@media (max-width: 1199px) {
		position: fixed;
		top: 50px;
		left: 0;
		background: var(--bs-body-bg);
		width: 100%;
		height: 100%;
		z-index: 45;
		opacity: 0;
		transition: all .3s ease;
		pointer-events: none;
		transform: translateX(-100%);
		overflow: hidden;
		touch-action: auto;

		.menu-open & {
			opacity: 1;
			pointer-events: auto;
			transition: all .3s ease;
			transform: translateX(0);
		}

		&__container {
			height: calc(100dvh - 72px);
			overflow-y: scroll;
			display: flex;
			flex-direction: column;
			overflow-x: hidden;
		}
	}
}


html.menu-open {
	position: relative;
	touch-action: none;
	overflow: hidden;
}



.product-content__sidebar {

}

.product-sidebar__services {
	margin-top: auto;
}

.product-content__content {
	&.h-100 {

		& > * {
			height: 100%;
			margin-top: 0;

			& > * {
				height: 100%;

				div {
					height: 100%;
				}

				iframe {
					height: 500px;
					border-radius: 0;

					@media (min-width: 768px) {
						height: 700px;
					}

					@media (min-width: 1200px) {
						height: 100%;
					}
				}
			}
		}
	}
}

.product-sidebar__service-list {
	margin-bottom: 5px;

	@media (min-width: 992px) {
		margin-bottom: 10px;
	}

	li {
		line-height: 1.4;
		font-size: 16px;

		@media (max-width: 1199px) {
			position: relative;
			padding-left: 10px;
			display: inline;
		}

		&::before {


			@media (max-width: 1199px) {
				position: absolute;
				content: "";
				top: 8px;
				left: -3px;
				width: 4px;
				height: 4px;
				background-color: #313131;
				border-radius: 50%;
			}
		}

		&:first-child {
			padding-left: 0;

			&::before {
				content: none;
			}
		}

		&:not(:last-child) {
			margin-right: 10px;

			@media (min-width: 1200px) {
				margin-bottom: 10px;
			}
		}

		&:last-child {
		}
	}

	a {
		text-decoration: none;
		color: #000;

		&:hover {
			color: var(--primary-color-hover);
		}
	}
}

.product-sidebar__service-more {
	font-weight: 600;
	text-decoration: none;

	&:hover {
		text-decoration: underline;
		color: var(--primary-color);
	}
}

.product-content__cards-list {
	display: flex;
	flex-wrap: wrap;

	&.table-view {
		row-gap: 100px;
		gap: 20px;
		padding-bottom: 20px;

		@media (min-width: 992px) {
			gap: 40px;
		}

		/*
		.product-card {
			box-shadow: 0 9px 41px 0 rgba(34, 69, 107, 0.12);
			background: #fff;
			padding: 15px;
			border-radius: 20px;

			@media (min-width: 768px) {
				padding: 20px;
			}
		}

		 */

		.product-card__wrap {
			gap: 40px;
		}

		.product-content__cards-item {
			width: 100%;
			position: relative;
			min-height: 360px;
		}

		.product-card__profile-col {
			@media (min-width: 1200px) {
				width: 24%;
			}

		}

		.product-card__info-col {
			@media (min-width: 1200px) {
				width: 76%;
			}

		}

		.product-card__profile {
			//width: 197px;
		}

		.product-card__info {
			//width: 400px;
		}

		.product-card__text {
			@include line-clamp(3);

			@media (min-width: 768px) {
				@include line-clamp(2);
			}
		}

		.product-card__contacts {
			@media (min-width: 992px) {
				max-width: 445px;
			}

			.btn {
				width: 100%;
				justify-content: center;

				@media (min-width: 480px) {
					width: unset;
					flex-grow: 1;
				}

				@media (min-width: 576px) {
					width: calc(50% - 10px);
				}

				@media (min-width: 992px) {
					width: 45%;
				}
			}

			.btn.product-card__profile-phone-btn,
			.btn.product-card__profile-phone-show {
				@media (min-width: 480px) {
					width: 45%;
				}

				@media (min-width: 576px) {
					width: calc(50% - 10px);
				}

				@media (min-width: 992px) {
					width: 38%;
				}
			}
		}

		.product-card__thumbs {
			//position: absolute;
			//bottom: 0;
			//right: 0;
			//width: calc(80% - 40px);
			margin-top: 5px;

			.swiper-slide {
				img {
					max-height: 60px;
					width: 60px;
				}
			}
		}
	}

	&.card-view {
		gap: 10px;
		padding-bottom: 20px;

		@media (min-width: 992px) {
			gap: 20px;
		}

		.recommendation-card__image {
			border-radius: 8px 8px 0 0;
		}

		.product-content__cards-item {
			width: 100%;

			@media (min-width: 380px) {
				width: calc(100% / 2 - 5px);
			}

			@media (min-width: 768px) {
				width: calc(100% / 3 - 7px);
			}

			@media (min-width: 992px) {
				width: calc(100% / 3 - 14px);
			}
		}

		.recommendation-card {
			border-radius: 20px;
			transition: all .2s ease;
			background-color: #fff;

			&:hover  {
				@media (min-width: 992px) {
					box-shadow: 2px 7px 10px 0 rgba(0, 0, 0, 0.1);
				}
			}
		}

		.favorite-icon {
			@media (min-width: 992px) {
				top: 5px;
				right: 5px;
			}
		}

		.product-card__contacts {
			gap: 10px;
		}

		.product-card__contacts .btn {
			justify-content: center;
			flex-basis: calc(50% - 5px);
			padding: 8px 2px;

			@media (min-width: 380px) {
				flex-basis: 100%;
				flex-grow: 1;
			}

			@media (min-width: 992px) {
				flex-basis: unset;
			}
		}

		.btn.product-card__profile-phone-btn,
		.btn.product-card__profile-phone-show {

			@media (min-width: 992px) {
				flex-basis: 45%;
			}
		}
	}
}

.product-content__cards {
	//margin: 20px 0 0;
}

.product-content__cards-filter {
	margin-bottom: 10px;
	align-items: center;

	@media (min-width: 992px) {
		margin-bottom: 0;
	}
}

.product-content__cards-item {
	width: 100%;

	@media (min-width: 576px) {
		width: calc(100% / 2 - 10px);
	}

	@media (min-width: 992px) {
		width: calc(100% / 2 - 25px);
	}
}

.product-content__cards-list {
	&.checkbox-visible {
		.product-card__checkbox {
			opacity: 1;
			pointer-events: auto;
		}
	}
}