.editor-form {
	input, textarea {
		&:active, &:focus {
			box-shadow: none;
			border-color: var(--primary-color) !important;
		}
	}

	textarea {
		min-height: 178px;

		@media (min-width: 768px) {
			min-height: 112px;
		}
	}
}

.editor-form__group {
	&:not(:last-child) {
		margin-bottom: 40px;

		@media (min-width: 768px) {
			margin-bottom: 60px;
		}
	}

	ul {
		//list-style: none;
		//padding: 0;

		@media (min-width: 576px) {
			column-count: 2;
			column-gap: 20px;
		}
	}
}

.announcement-editor__form {
	margin-bottom: 80px;
}

.editor-form__title {
	font-weight: 600;
	font-size: 20px;
	line-height: 140%;
	margin-bottom: 16px;

	@media (min-width: 768px) {
		margin-bottom: 20px;
	}
}

.editor-form__content {
	height: 100%;
}

.editor-form__content--inline {
	display: flex;
	flex-wrap: wrap;
	gap: 10px;
	align-items: center;

	span {
		line-height: 1.2;
	}
}

.form-control__make-active {
	margin-top: 5px;
	display: block;
	font-weight: 600;
	font-size: 16px;
	color: var(--primary-color);
	text-decoration: none;
}


[data-approval-input] {
	[disabled] {
		pointer-events: none;
		background-color: #f6f6f6;
		user-select: none;
	}
}

.editor-form__buttons {
	display: flex;
	flex-wrap: wrap;
	gap: 16px;
	height: 100%;
	align-items: center;

	a {
		text-decoration: none;
		color: var(--primary-color);
		font-weight: 700;
		font-size: 20px;
	}
}

.editor-form__group-items {

}


.editor-form__group-item-add {

	& > * {
		display: flex;
		flex-wrap: nowrap;
		align-items: center;
		gap: 16px;
		justify-content: center;
	}
}





.editor-form__photo-items {
	display: flex;
	flex-wrap: wrap;
	gap: 8px;

	@media (min-width: 992px) {
		gap: 16px;
	}

	#previewContainer {
		display: flex;
		flex-wrap: wrap;
		gap: 16px;
		height: 100%;
	}
}

.editor-form__file-picker,
.editor-form__photo-item {
	border-radius: 10px !important;
	width: calc(100% / 3 - 6px);
	height: 100px;

	@media (min-width: 576px) {
		height: 175px;
	}

	@media (min-width: 768px) {
		width: calc(100% / 4 - 6px);
	}

	@media (min-width: 992px) {
		width: calc(100% / 5 - 13px);
		height: 200px;
	}

	img {
		object-fit: cover;
		border: 1px solid #ccc;
		width: 100%;
		height: 100%;
		border-radius: 10px;
		user-select: none;
	}
}

.editor-form__file-picker {
	display: flex;
	position: relative;
	order: 2;
	background-color: #e8e8e8;
	flex-direction: column;
	justify-content: center;
	padding: 20px;
	text-align: center;

	&.w-100 {
		& + .editor-form__photo-item {
			width: 100%;

			img {
				width: 100%;
			}
		}
	}

	*:not(input) {
		position: relative;
		z-index: 2;
		pointer-events: none;
	}

	i {
		display: block;
		font-size: 40px;
		pointer-events: none;
	}

	input {
		position: absolute;
		top: 0;
		left: 0;
		font-size: 0 !important;
		border: none;
		padding: 0 !important;
		width: 100%;
		height: 100%;
		border-radius: 10px;
	}
}

.editor-form__file-picker-title {
	display: block;
	color: var(--primary-color);
	font-weight: 600;
	font-size: 20px;
	text-decoration: underline;
}

.editor-form__file-picker input::file-selector-button {
	width: 100%;
	height: 100%;
	border: none;
	margin: 0;
	background-color: #e8e8e8;
}




.editor-form__photo-item {
	position: relative;
	display: inline-block;
}


.editor-form__remove-photo {
	display: flex;
	align-items: center;
	justify-content: center;
	position: absolute;
	top: 6px;
	right: 6px;
	background: rgba(255, 255, 255, 1);
	border-radius: 50%;
	width: 19px;
	height: 19px;
	text-align: center;
	font-size: 10px;
	cursor: pointer;
	border: none;
	background: var(--bs-danger);
	color: #fff;

	@media (min-width: 768px) {
		width: 20px;
		height: 20px;
	}

	@media (min-width: 1200px) {
		background: #000;
	}
	
	&:hover {
		background: #000;
		color: #fff;

		@media (min-width: 1200px) {
			background: var(--bs-danger);
		}
	}
}



.form__main-buttons {
	display: flex;
	flex-wrap: wrap;
	gap: 12px;

	@media (min-width: 576px) {
		gap: 20px;
	}

	& > * {
		flex-grow: 1;

		@media (min-width: 576px) {
			max-width: 184px;
			width: 100%;
		}
	}
}

.editor-form__title-inner {
	font-weight: 600;
	font-size: 20px;
	color: var(--bs-font-color);
	margin-bottom: 20px;
}

.editor-form__group-links {
	display: flex;
	flex-direction: column;
}

.editor-form__sub-title {
	font-weight: 400;
	font-size: 16px;
	line-height: 1.4;
	color: var(--gray-color);

	@media (min-width: 992px) {
		font-size: 20px;
		margin-bottom: 40px;
	}
}