.bottom-navigation-bar {
	z-index: 40;
	padding: 0;
	background: #fff;

	@media (min-width: 1200px) {
		display: none;
	}

	.dropdown-menu {
		transform: translateY(10px);
		opacity: 0;
		transition: all 0.2s ease-in-out;
	}

	.show {
		transform: translateY(0);
		opacity: 1;
	}

	a {
		text-decoration: none;
	}

	& ~ footer {
		margin-bottom: 70px;

		@media (min-width: 992px) {
			margin-bottom: 0;
		}
	}
}

.bottom-navigation-bar__element {
	position: static;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	height: 100%;
	padding: 9px 10px;
	min-height: 70px;
	color: var(--bs-font-color);

	&:active {
		background: #f8f8f8;
	}

	i {
		font-size: 30px;

		&.icon-plates {

		}

		&.icon-checklist {
			font-size: 35px;
		}

		&.icon-find {
			font-size: 35px;
		}

		&.icon-dots-empty {
			font-size: 35px;
		}
	}

	span {
		font-weight: 400;
		font-size: 12px;
		line-height: 1.4;
	}

	.dropdown-toggle {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;

		&::after {
			content: none;
		}
	}

	.dropdown-menu {
		padding: 15px 0;
		position: absolute;
		margin-bottom: 0 !important;
		left: 0;
		width: 100%;
		border-radius: 20px 20px 0 0;
		border-color: transparent;
	}

	.dropdown-menu__user-info {
		ul {
			margin: 0;
			margin-top: 15px;
			padding: 0;
			list-style: none;

			li {
				&:not(:last-child) {
					margin-bottom: 14px;
				}
			}

			a {
				font-weight: 400;
				font-size: 16px;
				color: var(--bs-font-color);
				display: block;
				line-height: 1.4;
			}
		}
	}

	.user-nav-elements__user-info {
		.btn {
			width: 100%;
			max-width: 300px;
		}
	}

	.user-nav-elements__log-out {
		margin-top: 20px;
		font-weight: 600;
		font-size: 16px;
		color: var(--primary-color);

		a {
			color: inherit;
		}
	}

}

.dropdown-backdrop {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: rgba(0, 0, 0, 0.5);
	z-index: 39;
	display: none;
}

.user-info-element {
	display: flex;
	gap: 7px;
	margin-bottom: 15px;
	align-items: center;
}

.user-info-element__image {
	width: 40px;
	height: 40px;
	border-radius: 50%;
	overflow: hidden;

	img {
		max-width: 100%;
		max-height: 100%;
	}
}

.user-info-element__title {
	display: flex;
	flex-direction: column;
}

.user-info-element__name {
	font-weight: 600;
	font-size: 16px;
	margin-bottom: 0;
	line-height: 1.2;
}

.user-info-element__position {
	font-weight: 600;
	font-size: 12px;
	color: #6f6f6f;
	line-height: 1.2;
	margin-bottom: 0;
}