.page-partner {
	background-color: #fff;

	@media (min-width: 992px) {
		background-color: var(--bs-bg-gray);
	}
}

.pagination {
	margin: 0 -5px;
	flex-wrap: wrap;

	li {
		a {
			font-size: 16px;
			font-weight: 600;
			color: #d0d0d0;
			text-decoration: none;
			padding: 10px;

			@media (min-width: 992px) {
				font-size: 18px;
			}

			&:hover {
				color: var(--primary-color-hover);
			}

			i {
				font-size: 16px;
			}
		}

		&.active {
			a {
				color: var(--primary-color);
			}
		}

		&.disabled {
			pointer-events: none;
			opacity: 0.5;
		}

		&.dots {
			pointer-events: none;

			a {
				font-weight: 300;
				opacity: 0.5;
				color: #222;
			}
		}
	}
}