.recommendation {

}

.recommendation__items {
	display: flex;
	flex-wrap: wrap;
	gap: 10px;

	@media (min-width: 768px) {
		gap: 20px;
	}
}

.recommendation__item {
	width: calc(100% / 2 - 5px);

	@media (min-width: 768px) {
		width: calc(100% / 3 - 14px);
	}

	@media (min-width: 992px) {
		width: calc(100% / 4 - 15px);
	}
}