.vacancies {
	padding: 30px 0;

	@media (min-width: 992px) {
		padding: 60px;
	}

	.section-title {
		margin-bottom: 10px;

		@media (min-width: 992px) {
			font-size: 38px;
		}
	}
}

.vacancies__tabs {
	margin-top: 20px;

	@media (min-width: 992px) {
		margin-top: 40px;
	}

	[data-tabs="tabs-list"] {
		display: flex;
		flex-wrap: wrap;
		border: none !important;
		gap: 6px;
		margin-bottom: 20px;
		max-width: unset;

		@media (min-width: 992px) {
			gap: 12px;
		}

		& > * {
			background: #fff;
			border-radius: 34px;
			padding: 8px 14px;
			font-weight: 400;
			font-size: 16px;
			line-height: 136%;
			border: none !important;
			width: fit-content;
			flex-basis: unset;
			color: var(--bs-font-color);

			@media (min-width: 992px) {
				border-radius: 34px;
				padding: 10px 26px;
				font-size: 20px;
			}

			@media (min-width: 1200px) {
				font-size: 24px;
			}

			&.active {
				background: var(--primary-color);
				color: #fff;
			}
		}
	}
}

.vacancies__list {
	display: flex;
	flex-wrap: wrap;
	gap: 20px;
}

.vacancies__item {
	box-shadow: 0 9px 41px 0 rgba(34, 69, 107, 0.12);
	background: #fff;
	border-radius: 20px;
	padding: 16px;
	width: 100%;

	@media (min-width: 768px) {
		width: calc(100% / 2 - 10px);
	}

	@media (min-width: 992px) {
		padding: 24px;
	}
}

.vacancies__item-header {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
}

.vacancies__item-title {
	font-weight: 600;
	font-size: 20px;
	line-height: 136%;

	@media (min-width: 992px) {
		font-size: 24px;
	}
}

.vacancies__item-type {
	font-weight: 400;
	font-size: 16px;
	line-height: 136%;
	text-align: right;
	color: #6f6f6f;

	@media (min-width: 992px) {
		font-size: 20px;
	}
}

.vacancies__item-exp {
	font-weight: 400;
	font-size: 16px;
	line-height: 136%;
	color: #6f6f6f;

	@media (min-width: 992px) {
		font-size: 18px;
	}
}

.vacancies__item-salary {
	font-weight: 700;
	font-size: 24px;
	line-height: 136%;
	color: var(--primary-color);
	font-family: Montserrat, sans-serif;


}

.vacancies__item-footer {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	gap: 8px;

	.btn {
		font-size: 16px;
		padding: 10px 13px;

		@media (min-width: 576px) {
			padding: 10px 20px;
		}

		@media (min-width: 768px) {
			padding: 10px 14px;
		}

		@media (min-width: 992px) {
			font-size: 18px;
		}

		@media (min-width: 1200px) {
			padding: 10px 20px;
		}
	}
}

.vacancies__pagination {
	margin-top: 40px;
}



#vacancyModal {
	.vacancies__item {
		background: #eee;
		width: 100%;
	}

	.vacancies__item-header {
		display: flex;
		flex-wrap: wrap;
		gap: 10px;
		align-content: center;
		margin-bottom: 20px;

		@media (min-width: 992px) {
			justify-content: flex-start;
			gap: 30px;
		}

		& > * {
			font-size: 14px;
			align-self: center;

			@media (min-width: 992px) {
				font-size: 20px;
			}
		}

		.btn {
			padding: 12px 16px;
		}
	}

	.vacancies__item-descr {
		@media (min-width: 992px) {
			display: flex;
			justify-content: space-between;
		}
	}

	.vacancies__item-title {
		font-size: 24px;
		color: var(--bs-title-color);

		@media (min-width: 992px) {
			font-size: 38px;
		}
	}

	.vacancies__item-exp {
		font-size: 14px;
		color: #6f6f6f;

		@media (min-width: 992px) {
			font-size: 20px;
			margin-bottom: 30px;
		}
	}

	.vacancies__item-salary {
		font-size: 24px;
		color: var(--bs-title-color);

		@media (min-width: 992px) {
			font-weight: 700;
			font-size: 32px;
		}
	}

	.vacancies__item-footer {
		.btn {
			width: 100%;

			@media (min-width: 992px) {
				max-width: 170px;
			}
		}
	}
}

