.intro-inner {
	.section-title {
		font-size: 36px;

		@media (min-width: 992px) {
			font-size: 38px;
		}
	}

	p {
		@media (min-width: 992px) {
			font-size: 20px;
			max-width: 562px;
		}
	}
}

.intro-inner__plate {
	position: relative;
	background-repeat: no-repeat;
	background-position: center right -50px;
	background-size: cover;
	border-radius: 0;
	background-image: url('../img/bg/main-hero.svg');

	@media (min-width: 1400px) {
		background-position: center right -20px;
		background-size: 125%;
	}
}

.intro-inner__descr {
	padding: 40px 0 0;

	@media (min-width: 768px) {
		display: flex;
		flex-direction: column;
		padding: 40px 0;
		justify-content: center;
		height: 100%;
	}
}

.intro-inner__image {
	background-repeat: no-repeat;
	background-position: bottom center;
	background-size: contain;
	width: 100%;
	height: 67vw;
	max-height: 350px;
	margin-top: 10px;

	@media (min-width: 768px) {
		margin-top: 30px;
	}

	@media (min-width: 992px) {
		height: 450px;
		max-height: unset;
	}

	@media (min-width: 1200px) {
		height: 560px;
	}
}