.service-detail {
	//padding-bottom: 30px;

	.section-title {
		font-weight: 600;
		font-size: 30px;

		@media (min-width: 768px) {
			font-size: 34px;
		}

		@media (min-width: 1200px) {
			font-size: 38px;
		}

		& + .product-card__profile-rating {
			.product-card__profile-rating-value {
				font-size: 30px;
			}

			.product-card__profile-rating-icon {
				img {
					height: 30px;
				}
			}

		}
	}

	.swiper-navigation {
		.swiper-button-next,
		.swiper-button-prev {
			border-radius: 50%;
			background-color: #fff;
			width: 50px;
			height: 50px;

			&.swiper-button-disabled {
				pointer-events: auto;
				transform: scale3d(1, 1, 1);
			}

			&::after {
				display: none;
			}

			i {
				font-size: 20px;
				color: var(--bs-font-color);
			}

			&:hover {
				box-shadow: 2px 7px 10px 0 rgba(0, 0, 0, 0.1);
				i {
					color: var(--primary-color);
				}
			}
		}

		.swiper-button-next {
			right: -25px;
		}

		.swiper-button-prev {
			left: -25px;
		}
	}

	.serviceDetailSlider {
		border-radius: 8px;

		.swiper-slide {
			border-radius: 8px;
			overflow: hidden;

			a {
				display: block;
				height: 67.75vw;
				position: relative;

				@media (min-width: 768px) {
					height: 520px;
				}
			}

			span {
				display: block;
				background-repeat: no-repeat;
				background-position: center;
				background-size: cover;
				width: calc(100% + 10%);
				height: calc(100% + 10%);
				position: absolute;
				top: -5%;
				left: -5%;
				-webkit-filter: blur(13px);
				filter: blur(13px);
			}

			img {
				object-fit: contain;
				max-width: unset;
				height: 67.75vw;
				position: relative;
				z-index: 2;

				@media (min-width: 768px) {
					height: 520px;
				}
			}
		}
	}

	.serviceDetailSliderThumbs {
		margin-top: 20px;

		.swiper-slide {

			img {
				//width: 82px;
				height: 100px;
				border-radius: 8px;

				@media (min-width: 1200px) {
					height: 90px;
				}

				@media (min-width: 1400px) {
					height: 75px;
				}
			}
		}
	}

	.product-card__contacts {
		margin-top: 20px;
		margin-bottom: 30px;

		@media (min-width: 576px) {
			max-width: 400px;
			margin-top: 30px;
			margin-bottom: 40px;
		}

		@media (min-width: 768px) {
			max-width: 520px;
		}

		@media (min-width: 1200px) {
			margin-top: 25px;
		}

		.btn {
			justify-content: center;
			flex-basis: calc(50% - 10px);
			flex-grow: 1;
			min-width: unset;
			padding: 12px 8px;

			@media (min-width: 360px) {
				//flex-basis: calc(50% - 5px);
			}

			@media (min-width: 768px) {
				//flex-basis: 250px;
			}

			@media (min-width: 1200px) {
				flex-basis: 100%;
			}
		}

		.button-show-text {
			font-size: 14px;
		}
	}

	.product-card__profile {
		display: flex;
		flex-wrap: nowrap;
		gap: 10px;
		align-items: flex-start;

		@media (min-width: 1200px) {
			align-items: center;
			gap: 20px;
			margin-bottom: 20px;
		}
	}

	.product-card__profile-info {
		margin-top: 0;
		gap: 6px;
	}

	.product-card__user-pic {
		min-width: 80px;
		min-height: 80px;
		max-width: 80px;
		max-height: 80px;
		border-radius: 50%;
	}

	.product-card__profile-title {
		@media (min-width: 1200px) {
			font-size: 24px;
		}
	}

	.product-card__profile-score {
		margin-bottom: 0;
		gap: 6px;
		flex-direction: column;
		align-items: flex-start;

		a {
			text-decoration: none;
		}
	}

	.product-card__city {
		margin-top: 4px;
	}

	.partner-reviews {
		.partner-reviews__comments {
			margin-top: 20px;
		}
	}

	.product-card__profile-rating-reviews {
		font-size: 20px;
		font-weight: 500;
	}
}

.service-detail__col-content {
	@media (min-width: 1200px) {
		width: 74%;
	}
}

.service-detail__col-sidebar {
	@media (min-width: 1200px) {
		width: 24%;
	}
}

.service-detail__slider {
	margin-bottom: 30px;

	@media (min-width: 1200px) {
		margin-top: 30px;
	}
}

.service-detail__main-slider {
	position: relative;

	.favorite-icon {
		position: absolute;
		right: 0;

		@media (min-width: 1200px) {
			right: 10px;
			top: 10px;
		}
	}
}

.service-detail__mobile-block {
	margin-bottom: 10px;
}

.service-detail__service-price {
	font-weight: 600;
	font-size: 24px;
	color: var(--primary-color);
	margin: 0;

	@media (min-width: 768px) {
		font-size: 30px;
	}

	@media (min-width: 1200px) {
		font-size: 40px;
		margin-bottom: 65px;
		margin-top: 12px;
	}
}

.service-detail__subscription-btn {
	margin-top: 28px;
	min-width: 146px;
	padding: 10px 20px;
}

.service-detail__user-props {
	margin-top: 28px;
	margin-bottom: 0;

	li {
		display: flex;
		flex-wrap: nowrap;
		align-items: center;
	}

	i {
		color: var(--primary-color);
		font-size: 23px;
		margin-right: 10px;
		position: relative;
		top: -1px;
	}
}

.service-detail__all-announcements {
	max-width: 286px;
	width: 100%;
	color: var(--bs-font-color);
	padding: 18px 20px;
	margin-bottom: 28px;
	margin-top: 60px;
}

.service-detail__location {

	a {
		text-decoration: none;
		font-weight: 600;
		font-size: 20px;
		color: var(--primary-color);
	}
}

.service-detail__user-details-list {

}

.user-details-list {
	li {
		display: flex;
		gap: 10px;
		line-height: 140%;
		font-size: 16px;
		font-weight: 400;

		@media (max-width: 575px) {
			gap: 12px;
		}

		@media (min-width: 768px) {
			font-size: 20px;
		}

		span {
			color: #6f6f6f;

			@media (max-width: 575px) {
				width: calc(100% / 2 - 6px);
			}
		}

		p {
			@media (max-width: 575px) {
				width: calc(100% / 2 - 6px);
			}
		}

		&:last-child {
			p, span {
				margin-bottom: 0;
			}
		}
	}
}

.service-detail__services-list {
	li {
		display: inline-flex;
		flex-wrap: wrap;
		justify-content: space-between;
		align-items: baseline;
		width: 100%;
		margin-bottom: 5px;

		&:last-child {
			margin-bottom: 0;
		}

		& > * {
			margin-bottom: 0;
			display: inline-flex;
			font-weight: 400;
			font-size: 16px;
			line-height: 140%;
		}

		.service-detail__services-title,
		span:first-child {
			margin-right: 5px;
		}


		.service-detail__services-divider {
			border-bottom: 1px dotted #6F6F6F;
			flex-grow: 1;
			position: relative;
			line-height: 1;
			bottom: 3px;
			opacity: 0.5;
			height: 1px;
			align-self: auto;
		}

		.service-detail__services-price {
			font-weight: 600;
			font-size: 18px;
		}

		.service-detail__services-price,
		span:last-child {
			margin-left: 5px;
		}
	}
}