.card {
	box-shadow: 0 9px 41px 0 rgba(34, 69, 107, 0.12);
	background: #fff;
	padding: 15px;
	border-radius: 20px;
	border-color: transparent;

	@media (min-width: 768px) {
		padding: 20px;
	}

	&.dimmed {
		opacity: 0.6;
	}

	&.disabled {
		pointer-events: none;
		user-select: none;
	}

	&--outlined {
		border: 1px solid #d0d0d0;
		box-shadow: none;
	}
}

.product-card {
	font-size: 16px;

	.swiper {
		width: 100%;
		height: 70px;
		margin-left: auto;
		margin-right: auto;

		@media (min-width: 768px) {
			height: 80px;
		}

		a {
			max-height: 300px;
			width: 100%;

			img {
				max-height: 300px;
				object-position: center;
			}
		}
	}

	.productСardSlider {
		height: 80%;
		max-height: 300px;
		width: 100%;

		@media (min-width: 992px) {
			height: 80%;
		}
	}

	.productСardSliderThumbs {
		height: 20%;
		border-radius: 16px;
		overflow: hidden;

		.swiper-slide {
			width: 25%;
			height: 100%;
			opacity: 0.4;
			transition: all .2s ease;
		}

		.swiper-slide-thumb-active {
			opacity: 1;
		}
	}

	.swiper-slide {
		text-align: center;
		display: flex;
		justify-content: center;
		align-items: center;
		background-size: cover;
		background-position: center;

		a[data-fancybox] {
			outline: none !important;
			cursor: inherit;
		}

		img {
			display: block;
			width: 100%;
			height: 100%;
			object-fit: cover;
			border-radius: 8px;
			user-select: none;
		}
	}
}

.product-card__user-pic {
	width: 100%;
	height: 80vw;
	border-radius: 8px;
	overflow: hidden;

	@media (min-width: 576px) {
		height: 510px;
	}

	@media (min-width: 768px) {
		height: 220px;
	}

	@media (min-width: 1200px) {
		width: 197px;
		height: 197px;
	}

	img {
		width: 100%;
		height: 100%;
		object-fit: cover;
		object-position: top;
	}
}

.product-card__wrap {
	//display: flex;
	//flex-wrap: nowrap;
	//gap: 10px;
	//width: 100%;
	//flex-direction: column;

	@media (min-width: 992px) {
		//display: flex;
		//flex-wrap: nowrap;
		//gap: 15px;
		//flex-direction: row;
	}

	@media (min-width: 1200px) {
		//gap: 20px;
	}
}

.product-card__profile {
	position: relative;
}

.product-card__profile-info {
	display: flex;
	flex-wrap: wrap;
	gap: 10px;
	flex-direction: column;
	justify-content: flex-start;
	align-items: flex-start;
	margin-top: 10px;
}

.product-card__profile-slider {
	border-radius: 16px;
	overflow: hidden;
	position: relative;
}

.product-card__profile-icons {
	display: flex;
	width: 100%;
	height: 38px;
	position: absolute;
	bottom: 0;
	justify-content: flex-end;
	padding-right: 5px;

	* {
		position: relative;
		z-index: 2;
	}

	.message-icon {
		transition: all .2s ease;

		&:hover {
			transform: translateY(-2px);
		}
	}
}

.message-icon {
	width: 35px;
	height: 35px;
	background-image: url('../img/icons/message.svg');
	background-repeat: no-repeat;
	background-position: center;
	background-size: 21px;
	z-index: 2;
	filter: drop-shadow(0px 0px 7px rgba(0, 0, 0, 0.3));
}

.favorite-icon {
	width: 46px;
	height: 46px;
	background-image: url('../img/icons/heart.svg');
	background-repeat: no-repeat;
	background-position: center;
	background-size: 22px;
	z-index: 2;
	//filter: drop-shadow(0px 0px 7px rgba(0, 0, 0, 0.3));
	cursor: pointer;

	@media (min-width: 992px) {
		background-size: 28px;
	}

	&.active {
		background-image: url('../img/icons/heart-filled.svg');
		filter: none;
		background-size: 22px;

		@media (min-width: 992px) {
			background-size: 24px;
		}
	}
}

.product-card__favorite-icon {
	position: absolute;
	top: 25px;
	right: 25px;
	width: 26px;
	height: 26px;
	background-size: 26px;

	@media (min-width: 768px) {
		top: 20px;
		right: 20px;
	}
}

.product-card__profile-title {
	line-height: 1.2;
	margin-bottom: 0;
	font-weight: 600;
	font-size: 20px;
}

.product-card__profile-score {
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	width: 100%;
	margin-bottom: 10px;
	row-gap: 10px;
	column-gap: 20px;

	@media (min-width: 768px) {
		margin-bottom: 0;
		gap: 10px;
	}

	@media (min-width: 992px) {
		justify-content: space-between;
	}
}

.product-card__profile-rating {
	display: flex;
	flex-wrap: nowrap;
	gap: 6px;
	position: relative;
	top: -1px;
}

.product-card__profile-rating-value {
	font-weight: 500;
	font-size: 20px;
	position: relative;
	top: 1px;
}

.product-card__profile-rating-reviews {
	line-height: 1.2;
	font-weight: 600;
	font-size: 14px;
	color: var(--recommend-link-color);

	&:hover {
		color: var(--primary-color);
	}
}

.product-card__contacts {
	display: flex;
	flex-wrap: wrap;
	gap: 10px;
	margin-top: 10px;

	@media (min-width: 768px) {
		gap: 20px;
	}

	.btn {
		font-weight: 400;
		font-size: 14px;
		display: flex;
		flex-wrap: nowrap;
		align-items: center;
		padding: 12px 16px;
		margin: 0;

		i {
			font-size: 20px;
			margin-right: 10px;
		}
	}
}

.product-card__profile-phone-btn,
.product-card__profile-phone-show {
	padding: 15px 10px;
	min-height: auto;
	width: fit-content;
	margin-top: 10px;

	@media (min-width: 576px) {
		padding: 7px 10px;
	}

	@media (min-width: 992px) {
		font-size: 15px;
		margin-top: 15px;
	}

	@media (min-width: 1200px) {
		margin-top: 15px;
	}

	@media (min-width: 1400px) {
		font-size: 16px;
		margin-top: 10px;
	}
}

.product-card__profile-phone-btn {
	background-color: var(--primary-color-light);
	//min-width: 183.64px;
	//color: var(--primary-color);

	&:hover {
		background-color: var(--bs-success-color-hover) !important;
	}
	
	&:active {
		color: #fff !important;
	}
}

.product-card__profile-phone {
	display: block;
	color: #000;
	text-decoration: none;
	font-weight: 500;

	&:hover {
		color: var(--primary-color);
	}
}

.product-card__info {
	display: flex;
	flex-direction: column;
	order: 1;

	@media (min-width: 992px) {
		order: 2;
	}
}

.product-card__title {
	display: block;
	font-weight: 600;
	font-size: 16px;
	color: var(--recommend-link-color);
	text-decoration: none;
	line-height: 1.2;
	transition: all .2s ease;
	margin-bottom: 10px;

	@media (min-width: 768px) {
		font-size: 20px;
		margin-bottom: 0;
		max-width: 90%;
	}

	&:hover {
		color: var(--primary-color);
		//text-decoration: underline;
	}
}

.product-card__price-overview {
	font-weight: 600;
	font-size: 20px;
	color: var(--bs-font-color);

	@media (min-width: 768px) {
		max-width: 90%;
	}
}

.product-card__prices {
	margin-top: 12px;
	margin-bottom: 8px;
}

.product-card__price-item {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	font-weight: 600;
	font-size: 14px;

	&:not(:last-child) {
		margin-bottom: 8px;
	}

	* {
		margin-bottom: 0;
		line-height: 1;
		display: inline-flex;
	}

	span {
		font-weight: 600;
	}
}

.product-card__price-divider {
	border-bottom: 1px dotted #000;
	flex-grow: 1;
	margin: 0 5px;
	position: relative;
	line-height: 1;
	bottom: 3px;
	opacity: .5;
	font-weight: 300;
}

.product-card__more {
	display: inline-flex;
	width: fit-content;
	font-weight: 600;
	font-size: 14px;
	color: var(--recommend-link-color);
	text-decoration: none;

	&:hover {
		color: var(--primary-color);
		text-decoration: underline;
	}
}

.product-card__prices + .product-card__text {
	margin-top: 8px;
}

.product-card__text {

	font-size: 16px;
	font-weight: 400;
	line-height: 1.3;
	@include line-clamp(5);

	@media (min-width: 576px) {
		@include line-clamp(4);
	}

	@media (min-width: 992px) {
		@include line-clamp(5);
	}

	@media (min-width: 1200px) {
		@include line-clamp(5);
	}

	@media (min-width: 1400px) {
		@include line-clamp(6);
	}

	* {
		&:last-child {
			margin-bottom: 0;
		}
	}
}

.product-card__city {
	margin-bottom: 0;
	font-weight: 400;
	font-size: 14px;
	display: flex;
	flex-wrap: nowrap;
	align-items: center;
	line-height: 1.2;

	i {
		font-size: 20px;
		margin-right: 5px;

		@media (min-width: 768px) {
			margin-right: 10px;
		}

	}
}

.product-card__thumbs {
	margin-top: 10px;

	@media (min-width: 992px) {
		margin-top: 12px;
	}

	@media (min-width: 1200px) {
		margin-top: 15px;
	}

	@media (min-width: 1400px) {
		margin-top: 20px;
	}
}

.product-card__user-elements {
	display: flex;
	flex-direction: column;
	position: absolute;
	top: 20px;
	right: 16px;
	z-index: 10;

	@media (max-width: 767px) {
		padding: 5px;
		background-color: #fff;
		border-radius: 8px;
		right: 20px;
		gap: 5px;
	}

	& > * {
		text-decoration: none;
		cursor: pointer;
		height: 30px;

		i {
			font-size: 30px;
			color: var(--gray-color);
		}

		&:hover {
			i {
				color: var(--primary-color);
			}
		}
	}
}

.product-card__thumbs + .product-card__card-statistic {
	margin-top: 10px;
}

.product-card__card-statistic {
	margin-top: 20px;
}

.card-statistic {
	display: flex;
	flex-wrap: wrap;
	background: #f8f8f8;
	border-radius: 8px;
	padding: 8px 20px;
	justify-content: space-between;
	gap: 8px;
}

.card-statistic__item {
	display: flex;
	flex-wrap: nowrap;
	font-size: 14px;
	align-items: center;
	gap: 4px;

	i {
		font-size: 20px;
	}

	* {
		margin: 0;
		line-height: 1;
	}

	&--views {

	}
	&--favourites {
		i {
			font-size: 22px;
		}
	}
	&--get-contacts {
		i {
			font-size: 18px;
		}
	}
}


.product-card .product-card__image-slider {
	.swiper {
		border-radius: 8px;
		height: 77vw;
		max-height: 400px;

		@media (min-width: 576px) {
			height: 200px;
			max-height: 200px;
		}
	}
}


.product-card__checkbox {
	position: absolute;
	top: 10px;
	left: 10px;
	z-index: 2;
	opacity: 0;
	pointer-events: none;
}

.checkbox-plate {
	position: sticky;
	left: 0;
	background: #222;
	width: 100%;
	border-radius: 15px;
	padding: 11px 20px;
	display: flex;
	flex-wrap: wrap;
	color: #fff;
	align-items: center;
	gap: 10px;
	justify-content: space-between;
	z-index: 41;
	opacity: 0;
	pointer-events: none;
	bottom: 10px;

	@media (min-width: 768px) {
		margin-top: 20px;
	}

	@media (min-width: 992px) {
		padding: 20px;
		border-radius: 20px;

	}

	&.plate-visible {
		transition: all .1s ease;
		opacity: 0.9;
		pointer-events: auto;
	}

	.form-check {
		height: 26px;
	}

	.form-check-label {
		white-space: nowrap;
	}
}

.checkbox-plate__buttons {
	display: flex;
	flex-wrap: wrap;
	gap: 10px;
	overflow: hidden;

	* {
		flex-grow: 1;
		height: 44px;
		padding: 8px 20px;
		border-width: 2px;
		flex-shrink: 1;
	}
}