.partner-profile {
	.product-card__profile {
		display: flex;
		flex-wrap: nowrap;
		gap: 20px;
		align-items: center;

		@media (min-width: 1200px) {
			flex-direction: column;
			align-items: flex-start;
		}
	}

	.product-card__user-pic {
		order: 2;
		min-width: 80px;
		min-height: 80px;
		max-width: 80px;
		max-height: 80px;
		border-radius: 50%;

		@media (min-width: 400px) {
			min-width: 100px;
			min-height: 100px;
			max-width: 100px;
			max-height: 100px;
		}

		@media (min-width: 475px) {
			min-width: 110px;
			min-height: 110px;
			max-width: 110px;
			max-height: 110px;
		}

		@media (min-width: 576px) {
			min-width: 150px;
			min-height: 150px;
			max-width: 150px;
			max-height: 150px;
		}

		@media (min-width: 768px) {
			min-width: 200px;
			min-height: 200px;
			max-width: 200px;
			max-height: 200px;
		}

		@media (min-width: 992px) {
			min-width: 250px;
			min-height: 250px;
			max-width: 250px;
			max-height: 250px;
		}

		@media (min-width: 1200px) {
			order: 1;
			min-width: 142px;
			min-height: 142px;
			max-width: 142px;
			max-height: 142px;
		}
	}

	.product-card__profile-info {
		margin-top: 0;
		gap: 6px;

		@media (min-width: 768px) {
			gap: 20px;
		}

		@media (min-width: 1200px) {
			gap: 6px;
		}
	}

	.product-card__profile-score {
		margin-bottom: 0;
		flex-direction: column;
		align-items: flex-start;
		gap: 6px;

		@media (min-width: 768px) {
			gap: 20px;
		}

		@media (min-width: 1200px) {
			gap: 6px;
		}

		a {
			text-decoration: none;
		}
	}

	.product-card__city {
		margin-top: 4px;
	}

	.product-card__profile-description {
		order: 1;
		flex-grow: 1;

		@media (min-width: 1200px) {
			order: 2;
		}
	}

	.product-card__contacts {
		margin-top: 25px;

		@media (min-width: 576px) {
			max-width: 400px;
		}

		@media (min-width: 768px) {
			max-width: 520px;
		}

		@media (min-width: 992px) {
			margin-top: 0;
		}

		@media (min-width: 1200px) {
			margin-top: 25px;
		}

		.btn {
			justify-content: center;
			flex-basis: calc(50% - 10px);
			flex-grow: 1;
			min-width: unset;
			padding: 12px 8px;

			@media (min-width: 1200px) {
				flex-basis: unset;
			}
		}

		.button-show-text {
			font-size: 14px;
		}

		.product-card__profile-phone-btn {
			//min-width: 170px;
			//max-width: 170px;
		}
	}

	.partner-profile__cards-list {
		display: flex;
		gap: 10px;
		flex-wrap: wrap;

		@media (min-width: 992px) {
			gap: 20px;
		}
	}

	.partner-profile__cards-item {
		width: calc(100% / 2 - 5px);

		@media (min-width: 768px) {
			width: calc(100% / 3 - 7px);
		}

		@media (min-width: 992px) {
			width: calc(100% / 3 - 14px);
		}
	}

	.favorite-icon {
		@media (min-width: 992px) {
			top: 5px;
			right: 5px;
		}
	}

	.recommendation-card__image {
		overflow: hidden;
		border-radius: 10px 10px 0 0;

		@media (min-width: 992px) {
			border-radius: 20px 20px 0 0;
		}
	}

	.partner-profile__pagination {
		margin-top: 5px;
	}
}


.partner-profile__products {
	margin-top: 20px;

	@media (min-width: 992px) {
		margin-top: 45px;
	}

	@media (min-width: 1200px) {
		margin-top: 0;
	}
}

.partner-profile__slider {
	margin-top: 30px;
	border-radius: 10px;
	overflow: hidden;

	@media (min-width: 992px) {
		border-radius: 14px;
	}

	.swiper-slide {
		width: 154px !important;

		@media (min-width: 992px) {
			width: 191px !important;
		}


		a {
			overflow: hidden;
			width: 100%;
			display: block;
			height: 140px;

			@media (min-width: 992px) {
				height: 174px;
			}
		}
	}

	img {
		border-radius: 10px;
		object-fit: cover;
		object-position: center;

		@media (min-width: 992px) {
			border-radius: 14px;
		}
	}
}

.partner-profile__cards-list {
	position: relative;
}

.partner-reviews__add-btn {
	margin-top: 20px;
	margin-bottom: 30px;

	@media (min-width: 576px) {
		margin: 0;
	}

	.btn {
		width: 100%;
		max-width: 398px;
	}
}

.partner-reviews__comment-items {
	margin-top: 20px;
	display: flex;
	flex-direction: column;
	gap: 20px;

	@media (min-width: 992px) {
		gap: 30px;
	}
}

.partner-reviews__comment-item {
	padding-bottom: 15px;

	@media (min-width: 992px) {
		padding-bottom: 25px;
	}

	&:not(:last-child) {
		border-bottom: 1px solid #d0d0d0;
	}
}

.partner-reviews__pagination {
	margin-top: 20px;
}