.category-card {
	display: block;
	border-radius: 12px;
	padding: 12px;
	background-repeat: no-repeat;
	background-position: bottom 0 left 0;
	background-size: clamp(107px, 23vw, 180px);
	width: 100%;
	height: clamp(144px, 23vw, 180px);
	overflow: hidden;
	text-decoration: none;
	color: var(--card-color);
	font-size: 12px;
	font-weight: 600;
	line-height: 1.2em;
	flex-direction: column;
	justify-content: flex-end;
	position: relative;
	transition: all 0.15s ease;
	background-color: var(--card-bg-color);

	@media (min-width: 470px) {
		height: 150px;
		background-size: 120px;
	}

	@media (min-width: 768px) {
		font-size: 20px;
	}

	@media (min-width: 992px) {
		height: 100%;
		font-size: 18px;
		min-height: 200px;
		background-size: 160px;
	}

	@media (min-width: 1200px) {
		padding: 20px 18px 20px 20px;
		font-size: 20px;
		min-height: 248px;
		background-size: 185px;
	}

	&__link {
		display: flex;
		align-items: center;
		width: clamp(29px,8vw,40px);
		height: clamp(29px,8vw,40px);
		background-color: #fff;
		position: absolute;
		right: 12px;
		bottom: 12px;
		border-radius: 50%;
		justify-content: center;
		text-decoration: none;

		@media (min-width: 1200px) {
			width: 50px;
			height: 50px;
			right: 18px;
			bottom: 20px;
		}

		i {
			font-size: 16px;
			color: var(--bs-font-color);

			@media (min-width: 1200px) {
				font-size: 20px;
			}
		}
	}

	&:active {
		background-color: #eee;

		span {
			color: var(--primary-color-hover);
		}
	}

	@media (min-width: 992px) {
		&:hover {
			background-color: #eee;

			span {
				color: var(--primary-color-hover);
			}
		}
	}
}

.categories__item {
	&--big {

		.category-card {
			height: clamp(299px, 77vw, 340px);
			background-size: clamp(310px, 85vw, 390px);
			background-position: bottom center;
			padding: 20px 18px;

			@media (min-width: 992px) {
				min-height: 420px;
			}

			@media (min-width: 1200px) {
				min-height: 516px;
				background-size: 555px;
				padding: 30px 36px 30px 30px;
			}


			&::before {
				position: absolute;
				content: '';
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				background-color: transparent;
				background-repeat: no-repeat;
				background-position: center;
				background-size: cover;
				background-image: url('../img/bg/card-big-bg.png');
				pointer-events: none;
			}

			span {
				font-size: 18px;
				line-height: 111%;

				@media (min-width: 768px) {
					font-size: 24px;
					line-height: 120%;
				}

				@media (min-width: 992px) {
					line-height: 120%;
				}

				@media (min-width: 1200px) {
					font-size: 30px;
					line-height: 120%;
				}
			}
		}
	}
}
