.footer {
  //margin-top: 56px;
  background: var(--footer-bg-color);
  color: var(--footer-text-color);
  font-size: 12px;
  font-weight: 400;
  line-height: 160%;
  padding-top: 20px;

  @media (min-width: 768px) {
    font-size: 14px;
  }

  @media (min-width: 992px) {
    font-size: 16px;
  }

  @media (min-width: 1200px) {
    padding-top: 48px;
    margin-top: 0;
    margin-bottom: 0;
  }

  a {
    color: inherit;
    text-decoration: none;
    line-height: 100%;

    &:hover {
      color: var(--primary-color);
      text-decoration: none;
    }
  }
}

.floating-button.show + footer {
  margin-bottom: 70px;

  @media (min-width: 768px) {
    margin-bottom: 0;
  }
}

.footer__nav-list {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  gap: 10px;
  margin-bottom: 7px;

  @media (min-width: 768px) {
    flex-direction: row;
    gap: 28px;
  }

  @media (min-width: 992px) {
    margin-bottom: 10px;
  }
}

.footer__contacts {
  @media (min-width: 992px) {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}


.footer__info {
  margin-bottom: 28px;

  @media (min-width: 1200px) {
    margin-bottom: 59px;
  }

  ul {
    display: flex;
    flex-wrap: wrap;
    gap: 5px;
  }

  a {
    font-weight: 300;
    font-size: 10px;
    line-height: 160%;
    text-decoration: underline !important;
    color: #6f6f6f;

    @media (min-width: 768px) {
      font-size: 14px;
    }
  }
}

.footer__logo {
  margin-bottom: 29px;

  @media (min-width: 992px) {
    margin-bottom: 0;
  }

  .navbar-brand {
    display: block;
    width: 217px;
    margin-right: 0;
    padding: 0;

    @media (min-width: 992px) {
      width: 200px;
    }

    @media (min-width: 1200px) {
      width: 215px;
    }

    img, svg {
      max-width: 100%;
    }
  }
}

.footer__contacts-items {
  display: flex;
  flex-wrap: wrap;
  gap: 47px;
  align-items: center;

  @media (min-width: 992px) {
    gap: 47px;
  }
}

.footer__soc-list {
  margin-bottom: 0;
}

.footer__contacts-list {
  margin-bottom: 0;

  a {
    font-weight: 400;
    font-size: 14px;
    line-height: 160%;

    @media (min-width: 1200px) {
      font-size: 16px;
    }
  }
}

