.service-card {
	display: block;
	border-radius: 11px;
	padding: 20px 10px 15px;
	width: 100%;
	min-height: 112px;
	background-color: #F5F5F5;
	text-decoration: none;
	color: var(--bs-font-color);
	transition: all .15s ease;
	transform: scale3d(1, 1, 1);

	@media (min-width: 576px) {
		padding: 18px 14px 18px;
	}

	@media (min-width: 992px) {
		padding: 18px 12px 18px;
		width: unset;
	}

	img {
		transition: all .2s ease;
	}

	&:hover {
		//box-shadow: 2px 2px 10px -4px rgba(0,0,0,0.5);
		color: var(--primary-color);
		background-color: var(--card-bg-hover) !important;
		
		.service-card__title {
			color: #fff;
		}

		img {
			transform: scale3d(1.08,1.08,1);
			transition: all .2s ease;
		}
	}

	.service-card__title {
		display: flex;
		justify-content: center;
		flex-direction: column;
		margin: 10px 0 0;
		line-height: 1.13em;
		height: 40px;
		transition: all .15s ease;

		@media (min-width: 576px) {
			height: 42px;
		}

		@media (min-width: 768px) {
			height: 32px;
		}

		@media (min-width: 992px) {
			max-width: 80%;
			margin-left: auto;
			margin-right: auto;
		}
	}

	span {
		display: block;
		font-size: 11px;
		text-align: center;
		word-wrap: break-word;
		font-weight: 400;
		text-overflow: ellipsis;
		display: -webkit-box;
		-webkit-line-clamp: 3;
		-webkit-box-orient: vertical;
		line-height: 1.2em;

		overflow: hidden;
		position: relative;
		max-width: 100%;

		@media (min-width: 576px) {
			font-size: 12px;
		}

		@media (min-width: 768px) {
			font-size: 16px;
			-webkit-line-clamp: 2;
		}

		@media (min-width: 992px) {
			font-size: 14px;
		}

		@media (min-width: 1200px) {
			font-size: 16px;
		}
	}

	&__image {
		max-width: 95px;
		max-height: fit-content;
		margin: auto;

		@media (min-width: 992px) {
			max-width: 120px;
		}
	}

	&.circled {
		padding: 5px;
		background-color: transparent !important;

		@media (min-width: 992px) {
			padding: 0;
			max-width: 82px;
		}

		.service-card__image {
			max-width: fit-content;
			max-height: unset;
			margin: 0 auto 9px;
			overflow: hidden;
			border-radius: 50%;

			@media (min-width: 992px) {
				margin: 0 auto 10px;
			}
		}

		.service-card__more {
			display: flex;
			height: 100%;
			align-items: center;

			@media (min-width: 1200px) {
				padding: 0 40px 0 4px;
			}

			i {
				@media (min-width: 768px) {
					font-size: 22px;
				}
			}
		}

		img {
			border-radius: 50%;
			max-width: clamp(58px,15vw,70px);
			max-height: clamp(58px,15vw,70px);
			min-width: clamp(58px,15vw,70px);
			min-height: clamp(58px,15vw,70px);
			border: 1.71px solid #f8f8f8;

			@media (min-width: 768px) {
				max-width: 82px;
				max-height: 82px;
				min-width: 82px;
				min-height: 82px;
			}
		}

		span {
			max-width: 118px;
			margin-left: auto;
			margin-right: auto;

			@media (min-width: 576px) {
				max-width: 140px;
			}

			@media (min-width: 768px) {
				-webkit-line-clamp: 3;
				max-width: 160px;
			}

			@media (min-width: 992px) {
				-webkit-line-clamp: 2;
			}
		}
	}
}