.all-categories {
	background-color: var(--card-bg-color);
	position: relative;
	padding: 20px 0 28px;

	@media (min-width: 768px) {
		padding: 30px 0;
	}

	@media (min-width: 992px) {
		padding: 60px 0;
		margin-top: 12px;
	}

	@media (min-width: 1200px) {
		padding: 80px 0;
	}

	.section-title {
		position: relative;
		z-index: 2;
	}
}

.all-categories__image {
	@media (max-width: 767px) {
		display: none;
	}

	width: 100%;
	height: 260px;
	background-repeat: no-repeat;
	background-position: center;
	background-size: contain;

	@media (min-width: 992px) {
		height: 372px;
	}

	@media (min-width: 1200px) {
		width: 262px;
		margin-left: auto;
		margin-right: 22px;
	}
}

.all-categories__list {
	padding: 0;
	margin: 0;
	list-style-type: none;
	display: flex;
	flex-wrap: wrap;
	gap: 9.5px;

	li {
		font-size: 12px;
		line-height: 1.2;
		padding: 8px 12px;
		background-color: #fff;
		border-radius: 40px;

		@media (min-width: 768px) {
			padding: 12px 16px;
			border-radius: 42px;
			font-size: 16px;
		}

		&:hover {
			background-color: #e6efff;

			a {
				color: #0069b5;
			}
		}
	}

	a {
		text-decoration: none;
		color: var(--bs-title-color);
		font-weight: 400;
	}
}

.all-categories__buttons {
	margin-top: 29.5px;
	position: relative;
	display: flex;
	flex-wrap: wrap;
	gap: 20px;
	z-index: 2;

	@media (min-width: 576px) {
		margin-top: 30px;
	}

	@media (min-width: 1200px) {
		margin-top: 42px;
	}

	.btn {
		width: fit-content;
		min-height: 48px;
		padding: 9.5px 20px;

		@media (min-width: 1200px) {
			min-height: 52px;
		}

		span {
			display: inline-block;
			padding-left: 10px;
		}
	}
}