.cards-filter {
	font-size: 16px;
	padding: 10px 0;
	display: flex;
	gap: 20px;
	align-items: center;

	@media (min-width: 992px) {
		padding: 20px 0;
		gap: 40px;
	}

	.form-check-label {
		width: max-content;
	}

	.form-check {
		margin-bottom: 0;
	}
}

.cards-filter__mobile-element {
	.open-element {
		display: flex;
		align-items: center;
	}

	i {
		font-size: 25px;
	}
}

.sorting-elements,
.viewing-elements {
	display: flex;
	gap: 10px;
}

.sorting, .viewing {
	display: flex;

	* {
		margin-bottom: 0;
	}
}

.sorting {
	gap: 40px;
}


.sorting__name {
	white-space: nowrap;
	font-weight: 400;
	font-size: 16px;
	margin-right: 8px;
}

.viewing__value {
	white-space: nowrap;
	text-decoration: none;
	font-weight: 400;
	font-size: 16px;
	color: var(--recommend-link-color);

	&.current {
		font-weight: 600;
	}
}

.sorting__item {
	margin: 0 10px;

	span {
		cursor: pointer;
		user-select: none;
		display: none;
		color: var(--primary-color);
		font-weight: 600;
		min-width: max-content;


		&.current {
			display: block;
		}
	}
}

.sorting__icon {
	position: relative;
	padding-right: 20px;

	&::before {
	  position: absolute;
	  content: '';
	  top: calc(50% - 8px);
	  right: 0;
	  width: 15px;
	  height: 15px;
		background-repeat: no-repeat;
		background-position: center;
		background-size: contain;
	}

	&--min {
		&::before {
			background-image: url('../img/icons/down-arrow.svg');
			transform: rotate(180deg);
		}
	}

	&--max {
		&::before {
			background-image: url('../img/icons/down-arrow.svg');
		}
	}
}

.viewing__item {
	margin: 0 10px;
	display: flex;
	gap: 20px;

	span {
		color: #909090;
		font-weight: 500;
		cursor: pointer;
		user-select: none;

		&.current {
			color: var(--recommend-link-color);
		}
	}
}