html {
  transition: opacity .2s ease;

  &.loaded {
    transition: opacity .2s ease;
    opacity: 1 !important;
  }
}

html,
body {
  min-width: 320px;
  width: 100%;
  font-family: var(--bs-font-family);
  background-color: var(--bs-body-bg);
  color: var(--bs-font-color);
  font-size: var(--bs-font-size);
  font-weight: var(--bs-font-weight);
  line-height: var(--bs-line-height);
}

.bg-gray {
  background-color: var(--bs-bg-gray);
}

.section-padding {
  padding: 15px 0;

  @media (min-width: 768px) {
    padding: 30px 0;
  }

  @media (min-width: 1200px) {
    padding: 40px 0;
  }

  &--inner {
    padding: 20px 0;

    @media (min-width: 992px) {
      padding: 30px 0;
    }

    .block-backplate--mobile {
      padding: 16px;

      @media (min-width: 768px) {
        padding: 30px;
      }
    }

    &_small {
      padding: 10px 0;

      @media (min-width: 768px) {
        padding: 15px 0;
      }
    }
  }

  &--top-bigger {
    @media (min-width: 768px) {
      padding-top: 60px;
    }
  }
}

.section-margin {
  margin: 15px 0;

  @media (min-width: 768px) {
    margin: 30px 0;
  }

  @media (min-width: 1200px) {
    margin: 45px 0;
  }
}

.section-title {
  font-size: 20px;
  font-weight: 600;
  margin-bottom: 20px;
  color: var(--bs-font-color);

  @media (min-width: 768px) {
    font-size: 26px;
    margin-bottom: 24px;
  }

  @media (min-width: 1200px) {
    font-size: 36px;
    margin-bottom: 40px;
  }

  &--main {
    font-size: 20px;
    margin-bottom: 16px;

    @media (min-width: 375px) {
      text-align: center;
    }

    @media (min-width: 768px) {
      text-align: left;
      font-size: 28px;
    }

    @media (min-width: 1200px) {
      font-size: 38px;
      margin-bottom: 20px;
    }
  }

  &--bigger {
    font-size: 24px;

    @media (min-width: 768px) {
      font-size: 28px;
    }

    @media (min-width: 1200px) {
      font-size: 38px;
    }
  }

  &--color {
    color: var(--bs-title-color);
  }

  &--inner {
    font-size: 24px;

    @media (min-width: 768px) {
      font-size: 28px;
    }

    @media (min-width: 992px) {
      font-size: 32px;
    }

    @media (min-width: 1200px) {
      margin-bottom: 20px;
    }
  }
}

.section-title--mb-20 {
  margin-bottom: 20px;
}

.section-title__wrap {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 20px;
  align-items: center;
  margin-bottom: 20px;

  @media (min-width: 1200px) {
    margin-bottom: 38px;
  }

  .section-title {
    margin: 0;
  }
}

.section-title + nav .breadcrumb,
.section-title + .breadcrumb, {
  margin-top: 20px;
  margin-bottom: 40px;
}

.section-subtitle {
  line-height: 136%;

  @media (min-width: 992px) {
    font-size: 20px;
  }
}

.custom-checkbox {
  display: inline-block;
  position: relative;
  cursor: pointer;
  width: 23px;
  height: 23px;
  //padding-left: 30px; /* Расстояние между кастомным чекбоксом и текстом */

  input {
    display: none;
  }

  &::before {
    content: "";
    display: inline-block;
    position: absolute;
    left: 0;
    top: 0;
    width: 23px;
    height: 23px;
    border: 2px solid #6489FF;
    background-color: transparent;
    border-radius: 4px;
  }

  &::after {
    content: "";
    display: inline-block;
    position: absolute;
    left: 0;
    top: 0;
    color: #fff;
    font-size: 16px;
    visibility: hidden;
    background-image: url('../images/svg/check.svg');
    background-repeat: no-repeat;
    background-position: center;
    background-size: 15px;
    width: 100%;
    height: 100%;
  }
}

.custom-checkbox {
  &.active {
    &::before {

    }

    &::after {
      visibility: visible;
    }
  }
}

.list-style-none {
  list-style: none;
  padding: 0;
}

.btn {
  display: inline-block;
  color: var(--btn-text);
  background-color: var(--btn-bg);
  border-radius: 65px;
  padding: 12.5px 20px;
  outline: none;
  box-shadow: none;
  border: none;
  font-weight: 600;
  align-content: center;

  svg {
    path {
      transition: all .2s ease;
    }
  }

  @media (min-width: 768px) {
    font-size: 16px;
  }

  span {
    display: inline-block;
  }

  &:hover {
    cursor: pointer;
    background-color: var(--btn-bg-hover);
    color: var(--btn-text);
  }

  &:active {
    cursor: pointer;
    background-color: var(--bs-light-blue) !important;
    color: var(--primary-color) !important;

    svg {
      path {
        fill: var(--primary-color);
      }
    }
  }

  &--bordered {
    border: 1px solid var(--btn-bg);
  }

  &--outlined {
    background-color: transparent;
    color: var(--primary-color);
    border: 2px solid var(--primary-color);

    &:hover {
      color: #fff;
      border-color: var(--primary-color);
      background-color: var(--primary-color);
    }

    &_white {
      border-color: #fff;
      color: #fff;

      &:hover {
        background-color: #fff;
        color: var(--bs-font-color);
        border-color: #fff;
      }
    }
  }

  &--light {
    background-color: var(--bs-light-blue);
    color: var(--primary-color-light);
    border-color: var(--bs-light-blue);

    &:hover {
      color: #fff;
      border-color: var(--primary-color-light);
      background-color: var(--primary-color-light);
    }
  }

  &--dark {
    background-color: var(--gray-color);
    color: #fff;
    border-color: var(--gray-color);

    &:hover {
      color: #fff;
      border-color: var(--primary-color);
      background-color: var(--primary-color);
    }
  }

  &--gray {
    border-radius: 45px;
    background: #eee;
    color: var(--primary-color-light);
    font-weight: 600;
    font-size: 14px;
    padding: 10px 16px;

    &:hover {
      background: #f8f8f8;
      color: var(--primary-color-light);
    }

    @media (min-width: 768px) {
      font-size: 16px;
    }

    &_text-dark {
      color: var(--bs-font-color);

      &:hover {
        color: var(--bs-font-color);
      }
    }
  }

  &--white {
    background: #fff;
    color: var(--bs-font-color);

    &:hover {
      background-color: var(--bs-light-blue);
      color: var(--primary-color);
    }
  }

  &--hover-white {
    &:hover {
      background-color: #fff;
    }
  }

  &--hover-light {
    &:hover {
      background-color: var(--bs-light-blue);
      color: var(--primary-color);
    }
  }

  &--third {
    background: var(--bs-light-secondary);
    border-radius: 10px;
  }

  &--fourth {
    background: var(--primary-color);
    border-radius: 10px;
  }

  &--w-icon {
    display: flex;
    align-items: center;

    [class^="icon-"], [class*="icon-"] {
      font-size: 20px;
    }
  }

  &--light2 {
    background-color: var(--primary-color-light2);
    color: var(--primary-color);

    &:hover, &:active {
      background-color: var(--primary-color) !important;
      color: var(--btn-text) !important;
    }
  }

  &.w-100 {
    width: 100% !important;
    max-width: 100% !important;
  }
}

.btn-more-less {
  background-color: transparent !important;
  border: none;
  color: var(--primary-color);

  &:hover {
    color: var(--primary-color-hover);
  }

  &:active {
    background-color: transparent !important;
  }
}

input.form-control {
  border-radius: 65px;
  padding: 9.5px 20px;
  border: none;

  &.bordered {
    border: 1px solid #000;

    &:hover, &:focus {
      box-shadow: 0 0 0 0;
      border-color: var(--primary-color);
    }
  }

  &.border-gray {
    border: 1px solid #d0d0d0;

    &:hover, &:focus {
      box-shadow: 0 0 0 0;
      border-color: var(--primary-color);
    }
  }
}

select.form-select {
  border-radius: 65px;
  padding: 10px 30px 10px 20px;
  border: none;

  &.bordered {
    border: 1px solid #000;

    &:hover, &:focus {
      box-shadow: 0 0 0 0;
      border-color: var(--primary-color);
    }
  }
}

textarea.form-control {
  border-radius: 20px;
  border: none;
}

input.form-control, textarea, label {
  width: 100%;
}

input.form-control,
select.form-select,
textarea, .btn {
  min-height: 40px;

  @media (min-width: 1200px) {
    font-size: 16px;
  }
}

input.form-control,
select.form-select,
textarea.form-control {
  min-height: 40px;
  font-size: 16px;
}

.form-control.imask-hours {
  width: 70px;
  text-align: center;
  padding: 9.5px 9px;

  @media (min-width: 768px) {
    width: 90px;
    padding: 9.5px 20px;
  }

  &.is-valid,
  &.is-invalid,
  .was-validated &:valid,
  .was-validated &:invalid {
    width: 90px;

    @media (min-width: 768px) {
      width: 110px;
    }
  }
}

.form-control::placeholder {
  color: #adadad;
  font-weight: 300;
}

.form-check-label {
  line-height: 120%;
  font-size: 16px;

  .form-check-input {
    position: relative;
    top: -3px;
    cursor: pointer;
  }
}

input {
  &:active, &:focus {
    box-shadow: none !important;
  }
}

.checkbox__description {
  font-weight: 400;
  font-size: 10px;
  line-height: 126%;
  color: #6f6f6f;
  position: relative;
  top: 2px;

  a {
    color: var(--primary-color);
		text-decoration: none;
  }
}

.form-element-w-button {
  position: relative;

  .form-element-button {
    position: absolute;
    top: 0;
    right: 0;
    width: 60px;
    height: 100%;
    border: none;
    background-color: transparent;
    display: inline-flex;
    flex-direction: column;
    justify-content: center;
    padding: 0;
    align-items: center;

    &:focus-visible {
      outline: none;
    }

    i {
      display: block;
      font-size: 20px;
      color: #6F6F6F;
      transition: all .2s ease;
    }

    &:hover {
      i {
        color: #000;
      }
    }


    &.active {
      i {
        color: var(--primary-color);
      }
    }
  }
}

.form-control.is-invalid,
.was-validated .form-control:invalid,
.form-control.is-valid,
.was-validated .form-control:valid {
  & ~ .form-element-button {
    right: 20px;
  }
}

.form-control__tip-wrong {
  font-weight: 400;
  font-size: 14px;
  line-height: 1.4;
  color: var(--bs-danger);
  margin-top: 8px;
}

form {
  .hidden {
    display: none;
  }

  .active {
    display: block;
  }
}



.form-control--sms-number.is-invalid,
.was-validated .form-control--sms-number:invalid {
  border-color: var(--bs-danger) !important;

  & + .form-control__tip-wrong {
    display: block;
  }
}


.form-control--sms-number.is-invalid,
.was-validated .form-control--sms-number:invalid,
.form-control--sms-number.is-valid,
.was-validated .form-control--sms-number:valid {
  background-image: none !important;
}




.form-switch {
  padding-left: 3.5em;

  .form-check-label {
