}






.filter-element {

  .filter-element-icon {
    font-size: 12px;
    color: var(--primary-color);
    margin-left: 8px;

    &.icon-arrow-back {
      transform: rotate(270deg);
    }
  }

  .filter-select-container {
    display: flex;
    flex-wrap: nowrap;
    position: relative;
    align-items: center;
  }

  .select2-container {
    width: 100% !important;
    left: 0 !important;
  }

  .select2-selection--single {
    border: none;
    font-weight: 400;
    font-size: 12px;
    outline: none;
    background-color: transparent;
    border-radius: 0;
  }

  .select2-selection__rendered {
    color: var(--primary-color) !important;
    font-size: 16px;
    font-weight: 600;
    padding: 0 !important;
  }

  .select2-selection__arrow {
    display: none;
  }

  .select2-container--default .select2-search--dropdown .select2-search__field {
    border: none;
    outline: none;
    padding: 8px 0 0;
    border-radius: 0;
    border: none;
  }

  .select2-dropdown {
    overflow: hidden;
    box-shadow: 0 5px 5px -2px rgba(0, 0, 0, 0.1);
    border: 1px solid #ededed;
    width: calc(100% + 0px) !important;
    border-radius: 10px;
    padding: 10px;

    @media (min-width: 1200px) {
      border-radius: 20px;
      top: 10px;
    }
  }

  .select2-search__field,
  .select2-results__option {
    font-size: 16px;
    line-height: 1;
    white-space: nowrap;
    border-radius: 5px;
    padding: 10px 12px;

    @media (min-width: 1200px) {
      font-size: 16px;
      padding: 12px 15px;
      border-radius: 10px;
    }
  }

  .select2-results__option--highlighted.select2-results__option--selectable {
    background-color: #f8f8f8;
    color: var(--bs-font-color);
  }

  .select2-container--default .select2-results__option--selected {
    background-color: var(--primary-color);
    color: #fff;
  }

  .select2-results__option--disabled {
    opacity: 0;
    display: none;
  }

  .select2-results__options {
    scrollbar-width: thin;
    scrollbar-color: transparent transparent;
  }

  .simplebar-content-wrapper {
    scrollbar-width: thin;
  }

  .simplebar-track.simplebar-vertical .simplebar-scrollbar:before {
    background-color: #EEEEEE;
  }

  .simplebar-track.simplebar-vertical {
    top: 0;
    width: 6px;
  }

  .simplebar-vertical .simplebar-scrollbar:before {
    top: 2px;
    bottom: 2px;
    left: 2px;
    right: 0;
  }

  .select2-search--dropdown {
    border-bottom: 1px solid rgba(150,150,150,0.2);
  }

  &.mobile-filter-element {
    .select2-dropdown {
      position: fixed;
      bottom: 0;
      left: 0;
      padding-top: 10px;
      padding-bottom: 20px;
      border-radius: 20px 20px 0 0;
      max-height: 50vh;
    }

    .select2-search__field,
    .select2-results__option {
      padding: 15px 20px;
      border-radius: 10px;
      font-size: 18px;
    }

    .select2-container--default .select2-search--dropdown .select2-search__field {
      padding: 10px 20px 10px 0;
      font-size: 18px;
      background: transparent;
      border-radius: 0;
      margin: 0;
    }

    .select2-container--open .select2-dropdown {
      animation: mobile-select-appear .2s ease;
    }

  }

  &.ios-filter-element {
    .filter-select-element {
      -webkit-appearance: none;
      opacity: 0;
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
    }

    .ios-select-trigger {
      display: flex;
      align-items: center;
      color: var(--primary-color);
      font-weight: 600;
      white-space: nowrap;
    }

    .filter-select-container {
      position: relative;
    }

    .select2-selection--single .select2-selection__rendered {
      opacity: 0;
      pointer-events: none;
      touch-action: none;
      font-size: 0;
      width: 0;
    }
  }
}

@keyframes mobile-select-appear {
  from {
    transform: translateY(100%);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

.select2-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1040;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease;
}

.select2-backdrop.active {
  opacity: 1;
  visibility: visible;
}


.mobile-filter-element {
  display: flex;
  flex-wrap: nowrap;
}



